import React, { createRef } from 'react';
import Toolbar from './ToolBar';
import BottomPanel from './BottomPanel';

import './imagePreview.scss';
import NavBar from '../NavBar';
import { connect } from 'react-redux';
import { IAppStoreState } from 'client/src/types/declaration';
import { appSettings } from 'client/src/services/settings';
import ProcessPage from 'client/src/components/ProcessPage';
import MessagePopup, { TMessage } from 'client/src/components/MessagePopup';
import { STRINGS } from 'client/src/services/localization/strings';
import { isEdgesFound } from 'client/src/utils/edges';
import { pointsToArray } from 'client/src/services/wasm/utils';

interface IImagePreviewProps {
  image: string;
  warnings: TMessage[];
}

interface IImagePreviewState {
  isWarningMessagesShow: boolean;
  showProcessPage: boolean;
  maxHeight: number; // max height of container, need to resize image under iOS
}

class ImagePreview extends React.Component<IImagePreviewProps, IImagePreviewState> {
  private imageContainerRef = createRef<HTMLDivElement>();
  private imageParentContainerRef = createRef<HTMLDivElement>();

  public state: IImagePreviewState = {
    isWarningMessagesShow: false,
    showProcessPage: false,
    maxHeight: 0,
  };

  public componentDidMount(): void {
    this._setMaxHeight();
    this.setState({
      isWarningMessagesShow: false,
    });
    document.addEventListener('resize', this._setMaxHeight);
  }

  public componentWillUnmount(): void {
    document.removeEventListener('resize', this._setMaxHeight);
  }

  public componentDidUpdate(prevProps: Readonly<IImagePreviewProps>): void {
    if (prevProps.image !== this.props.image) {
      URL.revokeObjectURL(prevProps.image);
    }
  }

  private _setMaxHeight = () => {
    const parentPaneHeight = this.imageParentContainerRef.current.offsetHeight;

    if (parentPaneHeight) {
      this.imageContainerRef.current.style.maxHeight = parentPaneHeight + 'px';
      this.setState({
        maxHeight: parentPaneHeight,
      });
    } else {
      requestAnimationFrame(this._setMaxHeight);
    }
  };

  private _toggleWarnings = () => {
    this.setState({
      isWarningMessagesShow: !this.state.isWarningMessagesShow,
    });
  };

  private _onRotateChange = (start: boolean) => {
    this.setState({
      showProcessPage: start,
    });
  };

  public render() {
    return (
      <div className="ABBYY_Capture_Module_ImagePreview">
        <Toolbar onRotateChange={this._onRotateChange} />
        <div className="ABBYY_Capture_Module_ImageParentContainer" ref={this.imageParentContainerRef}>
          <div ref={this.imageContainerRef} className="ABBYY_Capture_Module_ImageContainer">
            {this.state.maxHeight && <img src={this.props.image} />}
            {this.props.warnings.length > 0 && !this.state.isWarningMessagesShow && (
              <div className="ABBYY_Capture_Module-Show_Warnings_Button" onClick={this._toggleWarnings} />
            )}
          </div>
        </div>
        <NavBar />
        {this.state.isWarningMessagesShow && (
          <MessagePopup
            messages={this.props.warnings}
            icon="warning"
            buttonText="Ok"
            onButtonClick={this._toggleWarnings}
          />
        )}
        <BottomPanel />
        {this.state.showProcessPage && <ProcessPage message={STRINGS.image_processing} />}
      </div>
    );
  }
}

export default connect((state: IAppStoreState) => {
  const currentImageResult = state.images[state.currentImageIndex];

  const ocrQualityConfidence = (currentImageResult.ocrQuality && currentImageResult.ocrQuality.confidence) || null;
  const newMessages: TMessage[] = [];

  if (ocrQualityConfidence && ocrQualityConfidence < appSettings.ocrQualityBound) {
    newMessages.push({ title: STRINGS.low_quality_title, text: STRINGS.low_quality_text });
  }

  const cropPoints = isEdgesFound(pointsToArray(currentImageResult.documentBoundary))
    ? currentImageResult.documentBoundary
    : currentImageResult.originalDocumentBoundary;

  if (!isEdgesFound(pointsToArray(cropPoints))) {
    newMessages.push({ title: STRINGS.crop_boundaries_not_found_title, text: STRINGS.crop_boundaries_not_found_text });
  }

  return {
    warnings: newMessages,
    image: window.URL.createObjectURL(new Blob([currentImageResult.capturedImage], { type: 'image/jpeg' })),
  };
})(ImagePreview);
