import { AnyAction } from 'redux';
import actionTypes from 'client/src/app/actions/actionTypes';
import { IAppStoreState, ICaptureResult } from 'client/src/types/declaration';

export const INITIAL_STATE: IAppStoreState = {
  currentMode: 'capture',
  currentImageIndex: -1,
  images: [],
  currentCameraIndex: 0,
};

const reducers: Record<string, (state: IAppStoreState, action: AnyAction) => IAppStoreState> = {
  [actionTypes.CHANGE_MODE]: (state: IAppStoreState, { payload: { mode } }: AnyAction) => {
    return {
      ...state,
      currentMode: mode,
    };
  },
  [actionTypes.ADD_IMAGE]: (state: IAppStoreState, { payload: { image } }: AnyAction) => {
    const newIndex = state.currentImageIndex + 1;

    state.images.splice(newIndex, 0, image);

    return {
      ...state,
      currentImageIndex: newIndex,
      currentMode: 'preview',
      images: [...state.images],
    };
  },
  [actionTypes.REPLACE_IMAGE]: (state: IAppStoreState, { payload: { image } }: AnyAction) => {
    state.images.splice(state.currentImageIndex, 1, image);

    return {
      ...state,
      currentImageIndex: state.currentImageIndex,
      currentMode: 'preview',
      images: [...state.images],
    };
  },
  [actionTypes.REPLACE_IMAGES]: (state: IAppStoreState, { payload: { imagesInfo } }: AnyAction) => {
    (imagesInfo as Array<{ index: number; result: ICaptureResult }>).forEach(({ index, result }) => {
      state.images.splice(index, 1, result);
    });

    return {
      ...state,
      images: [...state.images],
    };
  },
  [actionTypes.DELETE_CURRENT_IMAGE]: (state: IAppStoreState) => {
    state.images.splice(state.currentImageIndex, 1);

    return {
      ...state,
      currentImageIndex: Math.min(state.currentImageIndex, state.images.length - 1),
      images: [...state.images],
      currentMode: state.images.length > 0 ? 'preview' : 'capture',
    };
  },
  [actionTypes.GOTO_NEXT_IMAGE]: (state) => {
    return {
      ...state,
      currentImageIndex: Math.min(state.currentImageIndex + 1, state.images.length - 1),
    };
  },
  [actionTypes.GOTO_PREV_IMAGE]: (state) => {
    return {
      ...state,
      currentImageIndex: Math.max(state.currentImageIndex - 1, 0),
    };
  },
  [actionTypes.SWITCH_CAMERA]: (state, { newCameraIndex }) => {
    return {
      ...state,
      currentCameraIndex: newCameraIndex,
    };
  },
};

export default (state = INITIAL_STATE, action: AnyAction): IAppStoreState => {
  if (reducers.hasOwnProperty(action.type)) {
    return reducers[action.type](state, action);
  }

  return state;
};
