import { WasmImageQualityFlags } from 'client/src/services/wasm/CONSTANTS';

type TIMERS_KEYS = WasmImageQualityFlags.HightImageQuality | WasmImageQualityFlags.LowImageQuality;

type ITimers = {
  [key in TIMERS_KEYS]: number;
};

// time to first show pulse frame
const TIME_TO_FEEDBACK = 1000;

/**
 * business logic for camera feedback based on muddy frames
 * implements delayed updates
 */
export class ImageQualityChecker {
  /**
   * time since last wasm corresponding result
   */
  private readonly timers: ITimers;

  public feedback: WasmImageQualityFlags;

  constructor() {
    this.timers = {
      [WasmImageQualityFlags.HightImageQuality]: Date.now(),
      [WasmImageQualityFlags.LowImageQuality]: Date.now(),
    };

    this.feedback = WasmImageQualityFlags.HightImageQuality;
  }

  /**
   * main method for updated feedback
   * @param imageQualityFlags {number} - bit mask with flags: enum WasmImageQualityFlags
   * @param edgesCheckerFeedback {number} - feedback from EdgesChecker
   */
  public update = (imageQualityFlags: number, edgesCheckerFeedback: number) => {
    const time = Date.now();

    // muddy frames > timeToShowFeedback - show feedback searching for document
    if (imageQualityFlags === 1) {
      if (time - this.timers[WasmImageQualityFlags.LowImageQuality] > TIME_TO_FEEDBACK && edgesCheckerFeedback) {
        this.feedback = WasmImageQualityFlags.LowImageQuality;
      } else {
        this.feedback = edgesCheckerFeedback;
      }
      return;
    }

    this.feedback = WasmImageQualityFlags.HightImageQuality;
    this.timers[WasmImageQualityFlags.LowImageQuality] = time;
  };
}
