import React from 'react';

import { connect } from 'react-redux';
import { changeMode, deleteCurrentImage, replaceImage } from 'client/src/app/actions/actionCreators';
import { ActionCreator, AnyAction } from 'redux';
import ToolBarButton from './ToolBarButton';
import TopPanel from 'client/src/components/TopPanel';
import { IAppStoreState, ICaptureResult } from 'client/src/types/declaration';
import { rotateResult } from 'client/src/utils/rotateImage';
import { STRINGS } from 'client/src/services/localization/strings';

interface IToolBarProps {
  deleteCurrentImage: ActionCreator<AnyAction>;
  switchToRecaptureMode: ActionCreator<AnyAction>;
  switchToCropMode: ActionCreator<AnyAction>;
  replaceImage: ActionCreator<AnyAction>;
  currentImage: ICaptureResult;
  onRotateChange: (start: boolean) => void;
}

// tslint:disable:no-shadowed-variable
const Toolbar = ({
  currentImage,
  deleteCurrentImage,
  switchToRecaptureMode,
  switchToCropMode,
  replaceImage,
  onRotateChange,
}: IToolBarProps) => {
  const rotateImageClockwise = async () => {
    onRotateChange(true);

    const rotatedResult = await rotateResult(currentImage);

    replaceImage(rotatedResult);
    setTimeout(() => {
      onRotateChange(false);
    }, 100);
  };

  return (
    <TopPanel>
      <ToolBarButton
        title={STRINGS.retake}
        onClick={switchToRecaptureMode}
        image="retake"
        data-testid="preview-retake"
      />
      <ToolBarButton
        title={STRINGS.rotate}
        onClick={rotateImageClockwise}
        image="rotate"
        data-testid="preview-rotate"
      />
      <ToolBarButton title={STRINGS.crop} onClick={switchToCropMode} image="crop" data-testid="preview-crop" />
      <ToolBarButton title={STRINGS.delete} onClick={deleteCurrentImage} image="delete" data-testid="preview-delete" />
    </TopPanel>
  );
};

export default connect(
  (state: IAppStoreState) => ({
    currentImage: state.images[state.currentImageIndex],
  }),
  {
    deleteCurrentImage,
    replaceImage,
    switchToRecaptureMode: () => changeMode('recapture'),
    switchToCropMode: () => changeMode('crop'),
  }
)(Toolbar);
