import React from 'react';
import ReactDOM from 'react-dom';
import MessagePage from 'client/src/components/MessagePage/index';
import MobileDetect from 'mobile-detect';
import './errorPage.scss';

const showErrorPage = (message: string, onClickHandler: () => void): void => {
  const mobileDetect = new MobileDetect(navigator.userAgent);
  const deviceClass = mobileDetect.mobile() ? (mobileDetect.tablet() ? 'tablet' : 'mobile') : 'desktop';

  return ReactDOM.render(
    <div className={`ABBYY_Capture_Module__Container ${deviceClass} ABBYY_Capture_Module__Error`}>
      <div className="ABBYY_Capture_Module_CaptureView">
        <MessagePage message={message} />
        <div className="ABBYY_Capture_Module_CaptureView_ButtonContainer ">
          <div
            data-testid="capture-button-close"
            className="ABBYY_Capture_Module_CaptureView_Button ABBYY_Capture_Module_CaptureView_Button__Close_Button"
            onClick={onClickHandler}
          />
        </div>
      </div>
    </div>,
    document.getElementById('ABBYY_Capture_Module')
  );
};

export default showErrorPage;
