import * as React from 'react';
import MessagePage from 'client/src/components/MessagePage';
import WasmLoadingPage from 'client/src/components/WasmLoadingPage';
import { STRINGS } from 'client/src/services/localization/strings';
import { ICaptureViewState } from 'client/src/components/CaptureView';

const getErrorMessage = (cameraErrorMessage: string, scanError: boolean): string => {
  let errorMessage: string = '';

  if (cameraErrorMessage) {
    errorMessage = cameraErrorMessage;
  }
  if (scanError) {
    errorMessage = STRINGS.wasm_error;
  }
  return errorMessage;
};

const ShowMessages = (props: ICaptureViewState) => {
  return (
    <div className="ABBYY_Capture_Module__Messages">
      {(props.cameraErrorMessage || props.scanError) && (
        <MessagePage message={getErrorMessage(props.cameraErrorMessage, props.scanError)} />
      )}
      {props.loading && !(props.cameraErrorMessage || props.scanError) && <WasmLoadingPage message="" />}
    </div>
  );
};

export default ShowMessages;
