import ReactDOM from 'react-dom';
import { runApp, showLoadingPage } from 'client/src/app';
import { appSettings, appSettings as Settings, setAppOptions } from 'client/src/services/settings';
import { IWebCaptureInterface, IWebCaptureInitOptions } from 'client/src/types/index.d.ts';
import { base64JpegToUint8, uint8ToBase64Jpeg } from 'client/src/utils/base64Conversion';
import { ICaptureResult } from 'client/src/types/declaration';
import { WASM_STATE } from 'client/src/services/wasm/CONSTANTS';
import wasmAdapter from 'client/src/services/wasm/wasmAdapter';
import { setWasmWorker } from 'client/src/services/wasm-worker/wasm-worker-adapter';
import PromiseWorker from 'promise-worker-transferable';
import { addBoundariesToExif, getBoundariesFromExif } from 'client/src/utils/jpegMeta';
import showErrorPage from 'client/src/components/ErrorPage/index';
import { STRINGS } from 'client/src/services/localization/strings';
import isUserOptionsValid from 'client/src/services/validationUserOptions';

window.ABBYY = window.ABBYY || {};

let wasmState = WASM_STATE.None;
let initPromise: Promise<null>;

const createContainerIfNotExist = () => {
  if (!document.getElementById(window.ABBYY.WebCapture.containerId)) {
    const appContainer = document.createElement('div');

    appContainer.style.display = 'none';
    appContainer.setAttribute('id', window.ABBYY.WebCapture.containerId);
    appContainer.setAttribute('class', 'ABBYY_Capture_Module');
    appContainer.setAttribute('display', 'none');

    document.body.appendChild(appContainer);
  }
};

window.ABBYY.WebCapture = {
  containerId: 'ABBYY_Capture_Module',
  version: '1.8.1',

  /** initialize Capture scripts */
  init: async (initOptions: IWebCaptureInitOptions = {}) => {
    setAppOptions(initOptions);
    if (!initPromise) {
      initPromise = new Promise(async (resolve, reject) => {
        if (wasmState !== WASM_STATE.None) {
          resolve();
        }

        if (!window.WebAssembly) {
          reject('WebAssembly is not supported');
          initPromise = null;
          return;
        }

        createContainerIfNotExist();
        showLoadingPage(window.ABBYY.WebCapture.containerId);

        wasmState = WASM_STATE.Loading;

        // load wasm modules
        try {
          const worker = new Worker(
            appSettings.userOptions.wasmFilesPath + '/wasm-worker.js?version=1.8.1_07-04-20,05:16:15'
          );

          worker.onerror = () => {
            reject('Error loading wasm scripts');
          };

          const promiseWorker = new PromiseWorker(worker);

          setWasmWorker(promiseWorker);

          await promiseWorker.postMessage('ping');

          await wasmAdapter.loadWasmScripts(window.ABBYY.WebCapture.version);
          await wasmAdapter.checkLicense(Settings.userOptions.licenseFilePath);
        } catch (e) {
          wasmState = WASM_STATE.None;

          reject('Error loading wasm scripts: ' + e);

          initPromise = null;
          return;
        }

        wasmState = WASM_STATE.Initialized;
        resolve();
      });
    }

    return initPromise;
  },
  /**
   * Show UI for getting documents
   * @param {string} id  - uniq id for specific app
   */
  capture: async ({ images = [], ...configOptions }: IWebCaptureInterface = {}) => {
    return new Promise(async (resolve, reject) => {
      createContainerIfNotExist();
      document.getElementById(window.ABBYY.WebCapture.containerId).style.display = '';

      setAppOptions(configOptions);

      if (isUserOptionsValid(appSettings.userOptions)) {
        if (wasmState !== WASM_STATE.Initialized) {
          try {
            const licenseFilePath = configOptions.licenseFilePath || Settings.userOptions.licenseFilePath;
            const wasmFilesPath = configOptions.wasmFilesPath || Settings.userOptions.wasmFilesPath;

            await window.ABBYY.WebCapture.init({
              licenseFilePath,
              wasmFilesPath,
              localizedStrings: configOptions.localizedStrings,
            });
          } catch (e) {
            showErrorPage(STRINGS.wasm_license_error, window.ABBYY.WebCapture.stopCapture);
            reject(e);
            return;
          }
        }

        const decodedImgs = images.map((img) => ({
          ...img,
          capturedImage: base64JpegToUint8(img.capturedImage),
          originalImage: base64JpegToUint8(img.originalImage),
          originalDocumentBoundary: getBoundariesFromExif(img.originalImage),
        }));

        // tslint:disable-next-line:no-empty
        wasmAdapter.setWasmDocumentConfig().catch(() => {});

        runApp(decodedImgs, window.ABBYY.WebCapture.containerId)
          .then((resultImgs: ICaptureResult[]) => {
            // convert ot base64 imgs
            resolve(
              resultImgs.map((img) => ({
                ...img,
                capturedImage: uint8ToBase64Jpeg(img.capturedImage),
                originalImage: addBoundariesToExif(uint8ToBase64Jpeg(img.originalImage), img.originalDocumentBoundary),
              }))
            );
            window.ABBYY.WebCapture.stopCapture();
          })
          .catch((e) => {
            window.ABBYY.WebCapture.stopCapture();
            reject(e);
          });
      } else {
        showErrorPage(STRINGS.capture_param_error, window.ABBYY.WebCapture.stopCapture);
      }
    });
  },
  /**
   * hide app container
   */
  stopCapture: () => {
    document.getElementById(window.ABBYY.WebCapture.containerId).style.display = 'none';
    ReactDOM.unmountComponentAtNode(document.getElementById(window.ABBYY.WebCapture.containerId));
  },
};
