import PromiseWorker from 'promise-worker-transferable';

/**
 * just wasmWorker singleton for convenient import/usage
 */

let wasmWorker: PromiseWorker = null;

export const getWasmWorker = () => {
  return wasmWorker;
};

export const setWasmWorker = (worker: PromiseWorker) => {
  wasmWorker = worker;
};
