/** live capture from video stream */
import CanvasHelper from 'client/src/services/wasm/canvasHelper';
import { TStatus, WasmEdgesDetectionResultFlags, WasmImageQualityFlags } from 'client/src/services/wasm/CONSTANTS';
import { arrayToPoints, getScaledDimensions } from 'client/src/services/wasm/utils';
import { IAreaOfInterest } from 'client/src/types/declaration';
import { getWasmWorker } from 'client/src/services/wasm-worker/wasm-worker-adapter';
import WASM_ACTION_TYPES from 'client/src/services/wasm-worker/WASM_ACTION_TYPES';
import { IPoint } from 'client/src/types';
import { EdgesChecker } from 'client/src/services/wasm/edgesChecker';
import { ImageQualityChecker } from 'client/src/services/wasm/imageQualityChecker';
import isIos from 'client/src/utils/isIos';
import { appSettings, PreferredCameraResolution } from 'client/src/services/settings';

/**
 * capture process, transfer images to worker, get result and call callbacks
 */
class CaptureProcess {
  /**
   * is process running
   */
  public isRunning = false;
  /**
   * small scaled canvas, for performance, used for getting wasm data
   */
  private scaledCanvas: CanvasHelper;
  /**
   * original sized canvas, draw image from video
   */
  private originalCanvas: CanvasHelper;
  /**
   * original sized (= screen dimension) image in jpeg format, store best image
   */
  private bestFrameJpegData: Uint8ClampedArray;
  /**
   * original sized canvas (= screen dimension), store best image
   */
  private bestCanvas: CanvasHelper;
  /**
   * edges on best frame
   */
  private bestEdges: Int32Array;
  /**
   * scale between original canvas and scaledCanvas
   */
  private scale: number;
  private videoEl: HTMLVideoElement;
  /**
   * ready to send document to main process
   */
  private onBestDocumentGet: (bestFrame: Uint8ClampedArray, documentBoundary: IPoint[]) => void;
  /**
   * each frame report edges and status
   */
  private onNextFrame: (
    edges: Int32Array,
    feedbackString: WasmEdgesDetectionResultFlags | WasmImageQualityFlags,
    quality: number
  ) => void;
  /**
   * business logic for feedback & status
   */
  public edgesChecker: EdgesChecker;
  private imageQualityChecker: ImageQualityChecker;

  // video larger than area of interest so we draw image from video [-areaX,-areaY,
  // videoWidth, videoHeight] on canvas [areaWidth, areaHeight]
  private originalCanvasClip: number[];
  private scaledCanvasClip: number[];

  public start = async ({
    videoEl,
    areaOfInterest,
    frameBoundaryPadding,
    onBestDocumentGet,
    onNextFrame,
  }: {
    videoEl: HTMLVideoElement;
    areaOfInterest: IAreaOfInterest;
    frameBoundaryPadding: number;
    onBestDocumentGet: (bestFrame: Uint8ClampedArray, documentBoundary: IPoint[]) => void;
    onNextFrame: (edges: Int32Array, feedbackString: WasmEdgesDetectionResultFlags) => void;
  }) => {
    if (this.isRunning) {
      return;
    }
    this.isRunning = true;

    const { width, height } = areaOfInterest;
    const { scale, scaledWidth, scaledHeight } = getScaledDimensions(width, height);

    this.edgesChecker = new EdgesChecker();
    this.imageQualityChecker = new ImageQualityChecker();

    // original image
    this.originalCanvas = new CanvasHelper(width, height);
    // resized image (performance)
    this.scaledCanvas = new CanvasHelper(scaledWidth, scaledHeight);
    // best image
    this.bestCanvas = new CanvasHelper(width, height);
    this.scale = scale;

    this.videoEl = videoEl;
    this.onBestDocumentGet = onBestDocumentGet;
    this.onNextFrame = onNextFrame;

    this.originalCanvasClip = [-areaOfInterest.x, -areaOfInterest.y, videoEl.videoWidth, videoEl.videoHeight];
    this.scaledCanvasClip = this.originalCanvasClip.map((v) => v * this.scale);

    await getWasmWorker().postMessage({
      width: scaledWidth,
      height: scaledHeight,
      frameBoundaryPadding,
      type: WASM_ACTION_TYPES.START_IMAGE_DETECTION,
      enableSharpnessFilter: appSettings.userOptions.enableSharpnessFilter,
    });

    this.captureNextFrame();
  };

  public async stop() {
    if (this.isRunning) {
      this.isRunning = false;
      await getWasmWorker().postMessage({
        type: WASM_ACTION_TYPES.STOP_IMAGE_DETECTION,
      });
    }
  }

  private drawFrameAccordingOs = () => {
    if (isIos) {
      this.scaledCanvas.drawFrame(this.originalCanvas.canvas);
    } else {
      this.scaledCanvas.drawFrame(this.videoEl, this.scaledCanvasClip);
    }
  };

  /**
   * draw frame from video, transfer to worker, receive result and apply feedback logic
   */
  public captureNextFrame = async () => {
    if (!this.isRunning) {
      return;
    }

    this.originalCanvas.drawFrame(this.videoEl, this.originalCanvasClip);
    this.drawFrameAccordingOs();

    const bmpData = this.scaledCanvas.getBMPData();

    const result = await getWasmWorker()
      .postMessage(
        {
          bmpData: bmpData.data,
          type: WASM_ACTION_TYPES.CAPTURE_NEXT_FRAME,
        },
        [bmpData.data.buffer]
      )
      .then((message) => message.result);

    this.edgesChecker.update(result.edgesDetectionResultFlags);
    this.imageQualityChecker.update(result.imageQualityFlags, this.edgesChecker.feedback);

    if (result.isNewBest) {
      isIos && appSettings.userOptions.preferredCameraResolution === PreferredCameraResolution.Resolution4K
        ? (this.bestFrameJpegData = this.originalCanvas.getJpegData())
        : this.bestCanvas.drawFrame(this.originalCanvas.canvas);

      this.bestEdges = result.vertices;
    }

    if (
      result.status !== TStatus.ResultReady &&
      (result.imageQualityFlags === 0 || !appSettings.userOptions.enableSharpnessFilter)
    ) {
      // wasm status - active searching
      this.onNextFrame(result.vertices, this.edgesChecker.feedback, result.quality);
      requestAnimationFrame(this.captureNextFrame);
    } else if (result.imageQualityFlags !== 0 && appSettings.userOptions.enableSharpnessFilter) {
      // wasm status - dirty camera
      this.onNextFrame(new Int32Array([0, 0, 0, 0, 0, 0, 0, 0]), this.imageQualityChecker.feedback, result.quality);
      requestAnimationFrame(this.captureNextFrame);
    } else {
      // wasm found best frame
      this.stop();
      await this.returnResult();
    }
  };

  private returnResult = async () => {
    if (isIos && appSettings.userOptions.preferredCameraResolution === PreferredCameraResolution.Resolution4K) {
      this.onBestDocumentGet(this.bestFrameJpegData, arrayToPoints(this.bestEdges.map((v) => v / this.scale)));
    } else {
      this.onBestDocumentGet(this.bestCanvas.getJpegData(), arrayToPoints(this.bestEdges.map((v) => v / this.scale)));
    }
  };
}

export default new CaptureProcess();
