/**
 * documentSize и AspectRatio помогают улучшить поиск границ документа в Capture и  EdgesDetection сценариях.
 * Возможны следующие варианты:
 * 1. Заданы AspectRatio и documentSize. В таком случае при поиске границ мы используем диапазон из AspectRation, а потом
 *    при исправлении перспективных искажений мы используем известные соотношения сторон документа из documentSize.
 * 2. Задан только documentSize. В таком случае AspectRatio вычисляется из DucumentSize-а автоматически.
 * 3. Задан только AspectRatio. В таком случае он используется при поиске границ документа, но при исправлении
 *    перспективных искажение мы не делаем каких-то предположений и полагаемся на дефолтное поведение.
 */
export class CDocumentHints {
  constructor({
    aspectRatioMin = 0,
    aspectRatioMax = 0,
    focalLength = -1,
    documentSize = {
      width: 0,
      height: 0,
    },
  }) {
    this.aspectRatioMin = aspectRatioMin;
    this.aspectRatioMax = aspectRatioMax;
    this.focalLength = focalLength;
    this.documentSize = documentSize;
  }

  public aspectRatioMin: number; // Нижняя граница соотношения сторон
  public aspectRatioMax: number; // Верхняя граница соотношения сторон
  /** Возможны следующие варианты:
   *   - ( AspectRatioMin == 0.0 && AspectRatioMax == 0.0 ) - AspectRatio не учитывается
   *   - ( AspectRatioMin <= AspectRatioMax ) && ( AspectRatioMin >= 1.0 && AspectRatioMax >= 1.0 ) - ищется
   *      документ в указанном диапазоне AspectRatio
   * Остальные комбинации будут приводить к ошибке EC_InvalidArgument
   */
  public focalLength: number; // Фокусное расстояние камеры ( позволяет лучше исправлять перспективные искажения )
  /**  Если ( focalLength < 0.0 ), то мы имеем дело с неизвестным фокусным расстоянием мобильного устройства
   *   Если ( focalLength == 0.0 ), то мы не знаем не только фокусное расстояние, но и тип устройства
   *   Если ( focalLength > 0.0 ), то значение соответствует фактическому фокусному расстоянию устройства
   */
  public documentSize: {
    // Размер документа.
    /** width и height либо оба должны быть равны нулю, в таком случае счтается, что размер документа не задан,
     * либо они оба должны быть больше нуля.
     */
    width: number;
    height: number;
  };
}
