import piexifjs from 'piexifjs';
import { IPoint } from 'client/src/types';

export const addBoundariesToExif = (base64Img: string, boundaries: IPoint[]) => {
  const exifObj = piexifjs.load(base64Img);

  exifObj.Exif[piexifjs.ExifIFD.MakerNote] = JSON.stringify(boundaries);

  return piexifjs.insert(piexifjs.dump(exifObj), base64Img);
};

export const getBoundariesFromExif = (base64Img: string) => {
  const exifObj = piexifjs.load(base64Img);

  try {
    return JSON.parse(exifObj.Exif[piexifjs.ExifIFD.MakerNote]);
  } catch (e) {
    return null;
  }
};
