import { appSettings } from 'client/src/services/settings';
import { base64JpegToUint8 } from 'client/src/utils/base64Conversion';

const BITS_PER_PIXEL: number = 32;
const RESOLUTION: number = 300;

/**
 * helper class to work with canvas, that is not shown to user
 * draw video data, get BMP and JPEG, get image info array for wasm
 */
export default class CanvasHelper {
  private readonly width: number;
  private readonly height: number;

  public canvas: HTMLCanvasElement;
  private context: CanvasRenderingContext2D;

  /**
   * create canvas element
   * @param width
   * @param height
   */
  constructor(width: number, height: number) {
    this.width = width;
    this.height = height;

    this.canvas = document.createElement('canvas');
    this.canvas.width = width;
    this.canvas.style.width = width + 'px';
    this.canvas.height = height;
    this.canvas.style.height = height + 'px';
    this.context = this.canvas.getContext('2d');
  }

  public getBMPData(): ImageData {
    return this.context.getImageData(0, 0, this.width, this.height);
  }

  public getJpegData() {
    const base64Image = this.canvas.toDataURL('image/jpeg', appSettings.userOptions.jpegQuality / 100);

    return base64JpegToUint8(base64Image);
  }

  // draw frame from videoElement
  // can't use subclip areaOfInterest from large video because memory leak on safari
  // https://stackoverflow.com/questions/24387541/ios-7-1-1-canvas-drawimage-memory-leak
  // rect can be larger (to draw only part of videoEl)
  public drawFrame(videoEl: CanvasImageSource, rect?: number[]): void {
    const clip = rect || [0, 0, this.width, this.height];

    // tslint:disable-next-line:no-magic-numbers
    this.context.drawImage(videoEl, clip[0], clip[1], clip[2], clip[3]);
  }

  /**
   * special structure for WASM with image info
   * @param width
   * @param height
   */
  public static getcImageStruct(width: number, height: number) {
    // tslint:disable-next-line:no-magic-numbers 4 = RGBA
    return new Int32Array([width, height, width * 4, BITS_PER_PIXEL, RESOLUTION]);
  }
}
