import React from 'react';
import { connect } from 'react-redux';
import { goToNextImage, goToPrevImage } from 'client/src/app/actions/actionCreators';
import { IAppStoreState } from 'client/src/types/declaration';
import { ActionCreator, AnyAction } from 'redux';

import './navBar.scss';
import PageNumber from './PageNumber';

interface INavBarStateProps {
  current: number;
  max: number;
}

interface INavBarDispatchProps {
  goToNextImage: ActionCreator<AnyAction>;
  goToPrevImage: ActionCreator<AnyAction>;
}

class NavBar extends React.Component<INavBarStateProps & INavBarDispatchProps> {
  public render() {
    const prevClass: string =
      'ABBYY_Capture_Module__Navbar_prev' +
      (this.props.current < 1 ? ' ABBYY_Capture_Module__Navbar_prev--disabled' : '');
    const nextClass: string =
      'ABBYY_Capture_Module__Navbar_next' +
      (this.props.current >= this.props.max - 1 ? ' ABBYY_Capture_Module__Navbar_next--disabled' : '');

    return (
      <>
        {this.props.max > 1 && (
          <div className={prevClass} onClick={this.props.goToPrevImage} data-testid="navbar-prev" />
        )}
        <div className="ABBYY_Capture_Module__Navbar_pager" data-testid="navbar-pager">
          <PageNumber />
        </div>
        {this.props.max > 1 && (
          <div className={nextClass} onClick={this.props.goToNextImage} data-testid="navbar-next" />
        )}
      </>
    );
  }
}

export default connect(
  (state: IAppStoreState) => ({
    current: state.currentImageIndex,
    max: state.images.length,
  }),
  {
    goToNextImage,
    goToPrevImage,
  }
)(NavBar);
