import { appSettings, PreferredCameraResolution } from 'client/src/services/settings';
import { RefObject } from 'react';
import { isEdgesFound } from 'client/src/utils/edges';

const EDGES_WIDTH = 5;

/**
 * updates canvas, edges, fps, closer feedback
 */
export class CanvasController {
  private canvasRef: RefObject<HTMLCanvasElement>;

  private prevTimestamp: number;
  private startTimestamp: number;
  private frames: number;
  private edgeWidth: number;
  private delta: number;

  constructor(canvasRef: RefObject<HTMLCanvasElement>) {
    this.canvasRef = canvasRef;

    // debug timers
    this.prevTimestamp = 0;
    this.startTimestamp = 0;
    this.frames = 0;
    this.edgeWidth = EDGES_WIDTH;
  }

  /**
   * recalculate edge width based on dimensions
   * @param width
   * @param height
   */
  public updateDimensions = (width: number, height: number) => {
    const desiredWidth = (appSettings.cameraConstraints.width as ConstrainULongRange).ideal;
    const desiredHeight = (appSettings.cameraConstraints.height as ConstrainULongRange).ideal;

    const scale = Math.min(width / desiredWidth, height / desiredHeight);

    this.edgeWidth = Math.round(scale * EDGES_WIDTH);
  };

  /**
   * redraw edges & fps
   * @param edges
   * @param quality
   */
  public update = (edges: Int32Array, quality: number) => {
    this.clearEdges();

    if (isEdgesFound(edges)) {
      this.drawEdges(Array.from(edges));
    }

    this._drawFPS();
    this._setPreferredCameraResolution();
    this._drawQuality(quality);
  };

  /**
   * set preferredCameraResolution from userOptions
   * @private
   */
  private _setPreferredCameraResolution = () => {
    if (1000 / this.delta < 10) {
      appSettings.userOptions.preferredCameraResolution = PreferredCameraResolution.ResolutionFullHD;
    }
  };

  /**
   * clear timers
   */
  public clear = () => {
    this.prevTimestamp = 0;
    this.startTimestamp = 0;
    this.frames = 0;
  };

  /**
   * clear canvas
   */
  public clearEdges = () => {
    const ctx = this.canvasRef.current.getContext('2d');

    ctx.clearRect(0, 0, this.canvasRef.current.width, this.canvasRef.current.height);
  };

  /**
   * draw 4 lines by edges
   * @param edges
   */
  private drawEdges = (edges: number[]) => {
    const ctx = this.canvasRef.current.getContext('2d');

    ctx.beginPath();

    ctx.lineWidth = this.edgeWidth;
    ctx.strokeStyle = appSettings.ui.edgesColor;

    ctx.moveTo(edges[0], edges[1]);
    ctx.lineTo(edges[2], edges[3]);
    ctx.lineTo(edges[4], edges[5]);
    ctx.lineTo(edges[6], edges[7]);

    ctx.closePath();

    ctx.fillStyle = 'rgba(74, 142, 224, 0.33)';
    ctx.fill();

    ctx.stroke();
  };

  /**
   * draw fps
   * @private
   */
  private _drawFPS = () => {
    if (!this.startTimestamp) {
      this.startTimestamp = Date.now();
      this.prevTimestamp = this.startTimestamp;
      return;
    }

    this.delta = Date.now() - this.prevTimestamp;

    this.frames++;
    this.prevTimestamp = Date.now();

    if (appSettings.userOptions.debug) {
      const ctx = this.canvasRef.current.getContext('2d');

      ctx.font = '16px Arial';
      ctx.fillStyle = 'red';
      // tslint:disable-next-line:no-magic-numbers
      ctx.fillText(`${(1000 / this.delta).toFixed(1)} instant fps`, 10, 25);
      // tslint:disable-next-line:no-magic-numbers
      ctx.fillText(`${((this.frames * 1000) / (this.prevTimestamp - this.startTimestamp)).toFixed(1)} avg fps`, 10, 45);
    }
  };

  /**
   * draw quality
   * @param quality
   * @private
   */
  private _drawQuality = (quality: number) => {
    if (appSettings.userOptions.debug) {
      const ctx = this.canvasRef.current.getContext('2d');

      ctx.font = '16px Arial';
      ctx.fillStyle = 'blue';
      ctx.fillText(`${quality} quality`, this.canvasRef.current.width - 72, 25);
    }
  };
}
