import React, { RefObject } from 'react';
import cameraService from 'client/src/services/cameraService';

import './captureView.scss';
import { APP_MODES, IAppStoreState, ICaptureResult } from 'client/src/types/declaration';
import { connect } from 'react-redux';
import { addImage, changeMode, replaceImage, saveData, switchCamera } from 'client/src/app/actions/actionCreators';
import { ActionCreator, AnyAction } from 'redux';
import CaptureOverlay from 'client/src/components/CaptureView/CaptureOverlay';
import CaptureBottomPanel from 'client/src/components/CaptureView/CaptureBottomPanel';
import { appSettings } from 'client/src/services/settings';
import wasmAdapter from 'client/src/services/wasm/wasmAdapter';
import MessagePopup from 'client/src/components/MessagePopup';
import ShowMessages from 'client/src/components/CaptureView/CaptureMessage';
import ProcessPage from 'client/src/components/ProcessPage';
import { STRINGS } from 'client/src/services/localization/strings';
import { getScaledDimensions } from 'client/src/services/wasm/utils';
import { CanvasController } from 'client/src/components/CaptureView/canvasController';
import { WasmEdgesDetectionResultFlags, WasmImageQualityFlags } from 'client/src/services/wasm/CONSTANTS';
import { DOCUMENT_SIZE } from 'client/src/constants/documentSize';
import _find from 'lodash/find';
import { debounce } from 'lodash';

enum CaptureProcessStatus {
  'RUNNING' = 'RUNNING',
  'SWITCHING_CAMERA' = 'SWITCHING_CAMERA',
  'STOPPED' = 'STOPPED',
  'DISABLED' = 'DISABLED',
}

interface ICaptureViewDispatchProps {
  addImage: ActionCreator<AnyAction>;
  replaceImage: ActionCreator<AnyAction>;
  saveData: ActionCreator<AnyAction>;
  switchToPreview: ActionCreator<AnyAction>;
  switchCamera: ActionCreator<AnyAction>;
}

interface ICaptureViewStateProps {
  imagesLength: number;
  currentMode: APP_MODES;
  currentCameraIndex: number;
}

export interface ICaptureViewState {
  overlayText: string;
  showProcessPage: boolean;
  captureProcessStatus: CaptureProcessStatus;
  cameraErrorMessage: string;
  scanError: boolean;
  loading: boolean;
  isFrameVisible: boolean;
}

// dynamically get overrides
const FEEDBACK_STRINGS: Record<number, string> = Object.defineProperties(
  {},
  {
    [WasmEdgesDetectionResultFlags.NotFound]: {
      get: () => STRINGS.camera_alert_searching,
    },
    [WasmEdgesDetectionResultFlags.DocumentIsTooSmall]: {
      get: () => STRINGS.camera_alert_closer,
    },
    [WasmEdgesDetectionResultFlags.AspectRatioNotMatch]: {
      get: () => {
        if (appSettings.userOptions.aspectRatioMin === appSettings.userOptions.aspectRatioMax) {
          const document = _find(
            Object.values(DOCUMENT_SIZE),
            (doc) => doc.width / doc.height === appSettings.userOptions.aspectRatioMin
          );

          if (document) {
            // ID/A4 document/Letter-size document
            return `${STRINGS.waitingFor} ${document.name}`;
          }
        }

        return STRINGS.incorrectAspectRatio;
      },
    },
    [WasmEdgesDetectionResultFlags.Found]: {
      get: () => '',
    },
    [WasmImageQualityFlags.LowImageQuality]: {
      get: () => STRINGS.camera_alert_sharpness_filter,
    },
  }
);

class CaptureView extends React.Component<ICaptureViewDispatchProps & ICaptureViewStateProps, ICaptureViewState> {
  private galleryInputRef = React.createRef<HTMLInputElement>();
  private videoRef = React.createRef<HTMLVideoElement>();
  private canvasRef = React.createRef<HTMLCanvasElement>();
  private canvasController: CanvasController;
  private oldMetaContent: string;
  private cameras: MediaDeviceInfo[] = [];
  public frameMoveClass: string = '';

  public state = {
    overlayText: '',
    showProcessPage: false,
    captureProcessStatus: appSettings.userOptions.enableAutoCapture
      ? CaptureProcessStatus.STOPPED
      : CaptureProcessStatus.DISABLED,
    cameraErrorMessage: '',
    scanError: false,
    loading: true,
    isFrameVisible: false,
  };

  constructor(props: ICaptureViewDispatchProps & ICaptureViewStateProps) {
    super(props);

    this.canvasController = new CanvasController(this.canvasRef);
  }

  private _setViewPortMeta = () => {
    const viewportmeta: HTMLMetaElement = document.querySelector('meta[name="viewport"]');

    this.oldMetaContent = viewportmeta.content;
    viewportmeta.content = 'width=device-width, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no';
  };

  private _restoreViewPortMeta = () => {
    const viewportmeta: HTMLMetaElement = document.querySelector('meta[name="viewport"]');

    viewportmeta.content = this.oldMetaContent;
  };

  /**
   * callback when wasm process next frame
   * @param edges
   * @param feedbackString
   * @param quality
   */
  private _onNextFrame = (
    edges: Int32Array,
    feedbackString: WasmEdgesDetectionResultFlags | WasmImageQualityFlags,
    quality: number
  ) => {
    if (!this.canvasRef.current || this.state.captureProcessStatus !== CaptureProcessStatus.RUNNING) {
      return;
    }

    if (feedbackString !== WasmImageQualityFlags.LowImageQuality) {
      if (edges[0] !== 0 && edges[6] !== 0 && this.state.isFrameVisible) {
        this.setState({
          isFrameVisible: false,
        });
      }
    } else {
      this.setState({
        isFrameVisible: true,
      });
    }

    this.canvasController.update(edges, quality);

    this.setState({
      overlayText: FEEDBACK_STRINGS[feedbackString],
    });
  };

  private _onGetImage = (captureImageResult: ICaptureResult) => {
    this._onHandleProcessStart()
      .then(() => {
        this._stopCaptureProcess();

        if (this.props.currentMode === 'capture') {
          this.props.addImage(captureImageResult);
        } else {
          this.props.replaceImage(captureImageResult);
        }
      })
      .catch(this._handleError);
  };

  /**
   * get actual video dimensions on screen (not scaled)
   * @param videoRef
   * @private
   */
  private _getVideoFitSize = (videoRef: RefObject<HTMLVideoElement>) => {
    const containerWidth = videoRef.current.offsetWidth;
    const containerHeight = videoRef.current.offsetHeight;
    const width = videoRef.current.videoWidth;
    const height = videoRef.current.videoHeight;

    const visibleRatio = width / height;
    const naturalRatio = containerWidth / containerHeight;

    const coordinates = {
      width,
      height,
      x: 0,
      y: 0,
    };

    if (naturalRatio < visibleRatio) {
      coordinates.width = Math.floor((containerWidth * height) / containerHeight);
      coordinates.x = Math.floor((width - coordinates.width) / 2);
    } else {
      coordinates.height = Math.floor((containerHeight * width) / containerWidth);
      coordinates.y = Math.floor((height - coordinates.height) / 2);
    }

    return coordinates;
  };

  private _runNextCaptureProcess = async () => {
    // on display we can see only visualSize dimensions due to scaling to cover /  full screen with video
    const visualSize = this._getVideoFitSize(this.videoRef);

    // scaled to lower dimension for fast work, this will transfer to wasm
    const { scaledWidth, scaledHeight } = getScaledDimensions(visualSize.width, visualSize.height);

    this.canvasRef.current.width = scaledWidth;
    this.canvasRef.current.height = scaledHeight;

    this.canvasController.updateDimensions(scaledWidth, scaledHeight);

    if (appSettings.userOptions.enableAutoCapture) {
      try {
        await wasmAdapter.captureProcess.startCaptureProcess({
          videoEl: this.videoRef.current,
          areaOfInterest: visualSize,
          onGetImage: this._onGetImage,
          onNextFrame: this._onNextFrame,
          onStartHandleProcess: this._onHandleProcessStart,
        });
      } catch (e) {
        this._handleError();
      }
      this.setState({
        captureProcessStatus: CaptureProcessStatus.RUNNING,
      });
    }
  };

  private _handleError = () => {
    this.setState({
      scanError: true,
      showProcessPage: false,
    });
  };

  private _captureCurrent = async () => {
    await this._onHandleProcessStart();

    await wasmAdapter.captureProcess.stopCaptureProcess();

    const video = this.videoRef.current;
    const result = await wasmAdapter.getResultFromSource(
      video,
      video.videoWidth,
      video.videoHeight,
      this._getVideoFitSize(this.videoRef)
    );

    if (result) {
      this._onGetImage(result);
    }
  };

  private _onOrientationChange = debounce(async () => {
    if (appSettings.userOptions.enableAutoCapture) {
      this._stopCaptureProcess();

      const oldVideoWidth = this.videoRef.current.videoWidth;
      const startTime = Date.now();

      // wait for dimensions update
      await new Promise((resolve) => {
        const checkForUpdate = () => {
          if (this.videoRef.current.videoWidth !== oldVideoWidth || Date.now() - startTime >= 1000) {
            resolve();
          }
          requestAnimationFrame(checkForUpdate);
        };

        requestAnimationFrame(checkForUpdate);
      });

      await this._runNextCaptureProcess();
    }
  }, 100);

  public async componentDidMount(): Promise<void> {
    this._setViewPortMeta();
    try {
      this.cameras = await cameraService.getCameras();

      this._startCaptureProcess();
    } catch (e) {
      this._handleCameraError(e);
    }
  }

  private _manualStopCapture = () => {
    this._stopCaptureProcess();

    if (this.props.imagesLength > 0) {
      this.props.switchToPreview();
    } else {
      this.props.saveData();
    }
  };

  private _onHandleProcessStart: () => Promise<void> = () => {
    return new Promise<void>((resolve) => {
      this.setState(
        {
          showProcessPage: true,
        },
        () => {
          setTimeout(resolve, 0);
        }
      );
    }).catch(this._handleError);
  };

  private _handleCameraError = (e: Error) => {
    this.setState(
      {
        cameraErrorMessage: e.message || String(e),
      },
      () => {
        // in EDGE without timeout choose file input appears before rendering Capture screen
        setTimeout(() => {
          this.galleryInputRef.current.click();
        }, 0);
      }
    );
  };

  private _startCaptureProcess = async () => {
    cameraService
      .startStreamToVideo(this.cameras[this._getCameraIndex()], this.videoRef.current)
      .then(async () => {
        window.addEventListener('orientationchange', this._onOrientationChange);
        if (this.state.captureProcessStatus !== CaptureProcessStatus.DISABLED) {
          await this._runNextCaptureProcess();
        }
        this.setState({
          showProcessPage: false,
          loading: false,
          isFrameVisible: true,
        });
      })
      .catch(this._handleCameraError);
  };

  private _stopCaptureProcess = () => {
    if (this.state.captureProcessStatus === CaptureProcessStatus.RUNNING) {
      this.canvasController.clear();
      wasmAdapter.captureProcess.stopCaptureProcess();
      this.setState({
        captureProcessStatus: CaptureProcessStatus.STOPPED,
      });
    }
  };

  private _continueCapture = async () => {
    await this._runNextCaptureProcess();
  };

  private _renderOverlay = () => {
    // if zeroFrame=false (wasm found crop) - not show overlay
    // is waiting page or camera disabled or capture stopped - not show
    // if autoCapture = false - not show
    const isRender =
      !this.state.overlayText ||
      this.state.showProcessPage ||
      !!this.state.cameraErrorMessage ||
      this.state.captureProcessStatus !== CaptureProcessStatus.RUNNING ||
      !appSettings.userOptions.enableAutoCapture;

    if (!this.state.isFrameVisible && this.state.overlayText === 'Looking for document') {
      this.setState({
        isFrameVisible: true,
      });
    }

    return (
      <CaptureOverlay
        text={this.state.overlayText}
        isRender={isRender}
        moveFrameClass={this.frameMoveClass}
        isFrameVisible={this.state.isFrameVisible}
        enableAutoCapture={appSettings.userOptions.enableAutoCapture}
      />
    );
  };

  public componentWillUnmount(): void {
    this.setState({
      showProcessPage: false,
      isFrameVisible: false,
    });
    this._restoreViewPortMeta();
    cameraService.stopStream();
    this._stopCaptureProcess();
    window.removeEventListener('orientationchange', this._onOrientationChange);
  }

  public componentDidUpdate(
    prevProps: Readonly<ICaptureViewDispatchProps & ICaptureViewStateProps>,
    prevState: Readonly<ICaptureViewState>
  ): void {
    if (prevProps.currentCameraIndex !== this.props.currentCameraIndex) {
      this.canvasController.clearEdges();
      wasmAdapter.captureProcess.stopCaptureProcess();
      this.setState(
        {
          captureProcessStatus: CaptureProcessStatus.SWITCHING_CAMERA,
          overlayText: '',
        },
        () => {
          this._startCaptureProcess();
        }
      );
    }
  }

  private _nextCamera = () => {
    if (
      this.state.captureProcessStatus !== CaptureProcessStatus.SWITCHING_CAMERA ||
      !appSettings.userOptions.enableAutoCapture
    ) {
      this.props.switchCamera((this._getCameraIndex() + 1) % this.cameras.length);
    }
  };

  private _getCameraIndex = () => {
    return this.props.currentCameraIndex < this.cameras.length ? this.props.currentCameraIndex : 0;
  };

  private _activateMoveFrame = (): void => {
    if (
      this.state.captureProcessStatus === CaptureProcessStatus.SWITCHING_CAMERA &&
      appSettings.userOptions.enableAutoCapture &&
      this.frameMoveClass === ''
    ) {
      this.frameMoveClass = 'online_capture-frame_move';

      setTimeout(() => {
        this.frameMoveClass = '';
      }, 1700);
    }
  };

  public render() {
    this._activateMoveFrame();

    return (
      <div className="ABBYY_Capture_Module_CaptureView">
        <video
          ref={this.videoRef}
          className="online_capture-video_capture"
          muted={true}
          playsInline={true}
          autoPlay={true}
          loop={true}
        />
        <canvas ref={this.canvasRef} />
        {this._renderOverlay()}
        {!this.state.scanError &&
          !this.state.cameraErrorMessage &&
          this.state.captureProcessStatus === CaptureProcessStatus.STOPPED && (
            <MessagePopup
              messages={[{ title: STRINGS.use_autocapture_title, text: STRINGS.use_autocapture_text }]}
              icon="autocapture"
              buttonText={STRINGS.continue}
              onButtonClick={this._continueCapture}
            />
          )}
        {ShowMessages(this.state)}
        <CaptureBottomPanel
          captureCurrent={this._captureCurrent}
          stopCapture={this._manualStopCapture}
          onGetImage={this._onGetImage}
          beforeGetImage={this._onHandleProcessStart}
          onFileSelectButtonClick={this._stopCaptureProcess}
          disableCapture={
            this.state.captureProcessStatus !== CaptureProcessStatus.RUNNING &&
            appSettings.userOptions.enableAutoCapture
          }
          showFileLoadButton={!this.state.scanError}
          onError={this._handleError}
          galleryInputRef={this.galleryInputRef}
          currentCameraIndex={this._getCameraIndex()}
          maxCameras={this.cameras.length}
          changeCamera={this._nextCamera}
          cameraError={Boolean(this.state.cameraErrorMessage)}
        />
        {this.state.showProcessPage && <ProcessPage message={STRINGS.image_processing} />}
      </div>
    );
  }
}

export default connect(
  (state: IAppStoreState) => {
    return {
      currentMode: state.currentMode,
      imagesLength: state.images.length,
      currentCameraIndex: state.currentCameraIndex,
    };
  },
  {
    addImage,
    saveData,
    replaceImage,
    switchToPreview: () => changeMode('preview'),
    switchCamera,
  }
)(CaptureView);
