import configureStore from 'client/src/app/configureStore';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import React from 'react';
import App from 'client/src/components/App';
import WasmLoadingPage from 'client/src/components/WasmLoadingPage';

import './app.scss';
import { STRINGS } from 'client/src/services/localization/strings';
import { ICaptureResult } from 'client/src/types/declaration';

/**
 * @param {string} id  - uniq namespace for app instance
 * @param {string} containerId  - dom container
 */

export const runApp = async (imgs: ICaptureResult[], containerId: string) => {
  return new Promise<ICaptureResult[]>((resolve, reject) => {
    try {
      const store = configureStore(imgs, resolve);

      ReactDOM.render(
        <Provider store={store}>
          <App />
        </Provider>,
        document.getElementById(containerId)
      );
    } catch (e) {
      window.console.error('Error Capturing Images: ', e);
      reject(e);
    }
  });
};

export const showLoadingPage = (containerId: string) => {
  ReactDOM.render(
    <div id="ABBYY_Capture_Module__Loader_Container">
      <WasmLoadingPage message={STRINGS.app_loading} />
    </div>,
    document.getElementById(containerId)
  );
};
