import React, { createRef } from 'react';
import { IPoint } from 'client/src/types';
import { appSettings } from 'client/src/services/settings';
import { get, throttle } from 'lodash';
import { uint8ToBase64Jpeg } from 'client/src/utils/base64Conversion';

import styles from './cropLayer.scss';
import { findNearestPointIndex, getCoordsFromEvent } from './cropUtils';

interface ICropLayerProps {
  cropPoints: IPoint[];
  updatePoint: (i: number, updatedPoint: IPoint) => void;
  svgExtraSpace: number;
  imgWidth: number;
  imgHeight: number;
  img: Uint8ClampedArray;
}

interface ICropLayerState {
  showMagnifier: boolean;
}

interface IDragEl {
  index: number;
  node: SVGGeometryElement;
  startEventPos: { x: number; y: number };
  currentPos: { x: number; y: number };
}

type UserEvent = React.TouchEvent | React.MouseEvent;

const MAGNIFIER_SIZE = parseInt(styles.magnifierSize, 10);

const EDGES_COLOR = appSettings.ui.edgesColor;
const BORDER_RADIUS = 1;
const CIRCLE_RADIUS = 9;
const SELECTED_CIRCLE_RADIUS = 15;

const isMobile = 'ontouchstart' in window;

const EVENTS = isMobile
  ? {
      move: 'touchmove',
      end: 'touchend',
    }
  : {
      move: 'mousemove',
      end: 'mouseup',
    };

class CropLayer extends React.Component<ICropLayerProps, ICropLayerState> {
  // direct refs to html elements, for DnD performance
  private svgRef = createRef<SVGSVGElement>();
  private canvasRef = createRef<HTMLCanvasElement>();
  private magnifierRef = createRef<HTMLDivElement>();
  private lineRefs = [
    createRef<SVGLineElement>(),
    createRef<SVGLineElement>(),
    createRef<SVGLineElement>(),
    createRef<SVGLineElement>(),
  ];
  private pointRefs = [
    createRef<SVGGeometryElement>(),
    createRef<SVGGeometryElement>(),
    createRef<SVGGeometryElement>(),
    createRef<SVGGeometryElement>(),
  ];
  private dragEl: IDragEl = null;

  constructor(props: ICropLayerProps) {
    super(props);

    this.state = {
      showMagnifier: isMobile, // show loop only on mobile devices
    };
  }

  private _dragStart = (ev: UserEvent) => {
    // not react to click event on mobile
    if (isMobile && ev.nativeEvent instanceof MouseEvent) {
      return;
    }

    const eventPos = getCoordsFromEvent(ev.nativeEvent);

    // go through all points and find nearest
    const pointIndex = findNearestPointIndex(
      this.svgRef.current.getBoundingClientRect(),
      this.props.cropPoints,
      eventPos
    );

    if (pointIndex !== null) {
      this.dragEl = {
        index: pointIndex,
        node: this.pointRefs[pointIndex].current,
        startEventPos: eventPos,
        currentPos: {
          ...this.props.cropPoints[pointIndex],
        },
      };

      // update magnifier
      if (this.state.showMagnifier) {
        this.magnifierRef.current.style.display = 'block';
        this._updateMagnifier();
      }

      // redraw selected point
      this.forceUpdate();
    }
  };

  private _movePoint = throttle((ev: Event) => {
    if (!this.dragEl) {
      return;
    }

    this.dragEl.currentPos = this._getElementPos(this.dragEl.index, ev);

    const { x, y } = this.dragEl.currentPos;
    const nextLineIndex = this.dragEl.index === this.props.cropPoints.length - 1 ? 0 : this.dragEl.index + 1;

    // update point
    this.dragEl.node.setAttribute('transform', this._getTranslate({ x, y }));

    // update lines
    this.lineRefs[this.dragEl.index].current.setAttribute('x1', String(x));
    this.lineRefs[this.dragEl.index].current.setAttribute('y1', String(y));
    this.lineRefs[nextLineIndex].current.setAttribute('x2', String(x));
    this.lineRefs[nextLineIndex].current.setAttribute('y2', String(y));
  }, 100);

  private _dragMove = (ev: Event) => {
    if (!this.dragEl) {
      return;
    }

    ev.preventDefault();
    ev.stopPropagation();

    this._movePoint(ev);
    if (this.state.showMagnifier) {
      this._updateMagnifier();
    }
  };

  private _dragEnd = () => {
    if (!this.dragEl) {
      return;
    }

    this.props.updatePoint(this.dragEl.index, this.dragEl.currentPos);
    this.dragEl = null;
    if (this.state.showMagnifier) {
      this.magnifierRef.current.style.display = 'none';
    }
    this.forceUpdate();
  };

  private _getElementPos = (i: number, ev: Event) => {
    const { x, y } = getCoordsFromEvent(ev);

    return {
      x: Math.max(0, Math.min(this.props.cropPoints[i].x + x - this.dragEl.startEventPos.x, this.props.imgWidth)),
      y: Math.max(0, Math.min(this.props.cropPoints[i].y + y - this.dragEl.startEventPos.y, this.props.imgHeight)),
    };
  };

  private _getTranslate = ({ x, y }: { x: number; y: number }) => {
    return 'translate(' + x + ', ' + y + ')';
  };

  private _renderPoints = () => {
    const dragIndex = get(this.dragEl, 'index', -1);

    return (
      <>
        {this.props.cropPoints.map((cropPoint, i) => {
          return (
            <g
              key={i}
              transform={this._getTranslate(cropPoint)}
              data-id={i}
              ref={this.pointRefs[i]}
              data-testid={`crop-point-${i}`}
            >
              <circle
                r={dragIndex === i ? SELECTED_CIRCLE_RADIUS : CIRCLE_RADIUS}
                fill="transparent"
                stroke={EDGES_COLOR}
                strokeWidth={BORDER_RADIUS}
              />
              <circle fill="transparent" r={this.props.svgExtraSpace} style={{ cursor: 'pointer' }} />
            </g>
          );
        })}
      </>
    );
  };

  private _renderLines = () => {
    return (
      <>
        {this.props.cropPoints.map((p1, i) => {
          const prevPointIndex = i === 0 ? this.props.cropPoints.length - 1 : i - 1;
          const p2 = this.props.cropPoints[prevPointIndex];

          return (
            <line
              ref={this.lineRefs[i]}
              key={i}
              x1={p1.x}
              x2={p2.x}
              y1={p1.y}
              y2={p2.y}
              stroke={EDGES_COLOR}
              strokeWidth={BORDER_RADIUS}
            />
          );
        })}
      </>
    );
  };

  /**
   * update magnifier position & background image
   */
  private _updateMagnifier = () => {
    const mag = this.magnifierRef.current;
    const pos = this.dragEl.currentPos;

    mag.style.left = pos.x - MAGNIFIER_SIZE / 2 + 'px';
    mag.style.top = pos.y - 100 + 'px';

    mag.style.backgroundPosition = `${-pos.x + MAGNIFIER_SIZE / 2}px ${-pos.y + MAGNIFIER_SIZE / 2}px`;

    this._updateMagnifierCanvas();
  };

  /**
   * draw lines in magnifier canvas
   */
  private _updateMagnifierCanvas = () => {
    const canvas = this.canvasRef.current;
    const ctx = canvas.getContext('2d');
    const pts = this.props.cropPoints.map((point) => ({
      x: point.x - this.dragEl.currentPos.x + MAGNIFIER_SIZE / 2,
      y: point.y - this.dragEl.currentPos.y + MAGNIFIER_SIZE / 2,
    }));

    pts[this.dragEl.index] = {
      x: MAGNIFIER_SIZE / 2,
      y: MAGNIFIER_SIZE / 2,
    };

    ctx.clearRect(0, 0, MAGNIFIER_SIZE, MAGNIFIER_SIZE);

    ctx.beginPath();
    ctx.lineWidth = 1;
    ctx.strokeStyle = appSettings.ui.edgesColor;

    ctx.moveTo(pts[0].x, pts[0].y);
    ctx.lineTo(pts[1].x, pts[1].y);
    ctx.lineTo(pts[2].x, pts[2].y);
    ctx.lineTo(pts[3].x, pts[3].y);

    ctx.closePath();
    ctx.stroke();
  };

  private _updateMagnifierProps = () => {
    this.magnifierRef.current.style.backgroundSize = `${this.props.imgWidth}px ${this.props.imgHeight}px`;
    this.magnifierRef.current.style.backgroundImage = `url("${uint8ToBase64Jpeg(this.props.img)}")`;
  };

  public componentDidUpdate(): void {
    if (this.state.showMagnifier) {
      this._updateMagnifierProps();
    }
  }

  public componentDidMount(): void {
    document.addEventListener(EVENTS.move, this._dragMove, { passive: false });
    document.addEventListener(EVENTS.end, this._dragEnd, true);

    if (this.state.showMagnifier) {
      this._updateMagnifierProps();
    }
  }

  public componentWillUnmount(): void {
    document.removeEventListener(EVENTS.move, this._dragMove);
    document.removeEventListener(EVENTS.end, this._dragEnd);
  }

  public render() {
    const viewBox = `${-this.props.svgExtraSpace} ${-this.props.svgExtraSpace} ${this.props.imgWidth +
      2 * this.props.svgExtraSpace} ${this.props.imgHeight + 2 * this.props.svgExtraSpace}`;

    return (
      <div>
        <svg
          className="ABBYY_Capture_Module__CropView_CropLayer"
          ref={this.svgRef}
          onMouseDown={this._dragStart}
          onTouchStart={this._dragStart}
          viewBox={viewBox}
        >
          {this._renderLines()}
          {this._renderPoints()}
        </svg>
        {this.state.showMagnifier && (
          <div className="ABBYY_Capture_Module__CropView_Magnifier" ref={this.magnifierRef}>
            <canvas ref={this.canvasRef} width={MAGNIFIER_SIZE} height={MAGNIFIER_SIZE} />
          </div>
        )}
      </div>
    );
  }
}

export default CropLayer;
