import { Store, AnyAction, Dispatch } from 'redux';
import actionTypes from '../actions/actionTypes';
import { IAppStoreState, IOnSave } from 'client/src/types/declaration';

export default (returnToExternal: IOnSave) => {
  return (store: Store<IAppStoreState>) => (next: Dispatch<AnyAction>) => (action: AnyAction) => {
    if (action.type === actionTypes.SAVE_DATA) {
      returnToExternal(store.getState().images);
    }

    return next(action);
  };
};
