// tslint:disable:no-bitwise

import { WasmEdgesDetectionResultFlags } from 'client/src/services/wasm/CONSTANTS';

type TIMERS_KEYS =
  | WasmEdgesDetectionResultFlags.Found
  | WasmEdgesDetectionResultFlags.AspectRatioNotMatch
  | WasmEdgesDetectionResultFlags.DocumentIsTooSmall;

type ITimers = {
  [key in TIMERS_KEYS]: number;
};

// time to first show pulse frame
const TIME_TO_FEEDBACK = 1000;

/**
 * business logic for camera feedback based on found boundaries statuses
 * implements delayed updates
 */
export class EdgesChecker {
  /**
   * time since last wasm corresponding result
   * found, good ratio and document is large enough   respectively
   */
  private readonly timers: ITimers;

  public feedback: WasmEdgesDetectionResultFlags;

  constructor() {
    this.timers = {
      [WasmEdgesDetectionResultFlags.Found]: Date.now(),
      [WasmEdgesDetectionResultFlags.AspectRatioNotMatch]: Date.now(),
      [WasmEdgesDetectionResultFlags.DocumentIsTooSmall]: Date.now(),
    };

    this.feedback = WasmEdgesDetectionResultFlags.Found;
  }

  /**
   * main method for updated feedback
   * @param edgesDetectionResultFlags {number} - bit mask with flags: enum WasmEdgesDetectionResultFlags
   */
  public update = (edgesDetectionResultFlags: number) => {
    // main problem with unifying logic different flags (positive & negative)
    // 0 = no edges(negative, but cannot be used in bitmask),
    // 1  = edges (positive, can be masked)
    // 2,4 = aspectRation & smallDocuments (negative, can be masked)
    const time = Date.now();

    // no edges & interval > timeToShowFeedback - show feedback searching for document
    if (edgesDetectionResultFlags === WasmEdgesDetectionResultFlags.NotFound) {
      if (time - this.timers[WasmEdgesDetectionResultFlags.Found] > TIME_TO_FEEDBACK) {
        this.feedback = WasmEdgesDetectionResultFlags.NotFound;
      }
      return;
    }

    // order of conditions determines feedback priority
    const feedbacks: TIMERS_KEYS[] = [
      WasmEdgesDetectionResultFlags.DocumentIsTooSmall,
      WasmEdgesDetectionResultFlags.AspectRatioNotMatch,
    ];

    feedbacks.forEach((v) => {
      if (edgesDetectionResultFlags & v) {
        // time passed or transition from NotFound - show feedback
        this.timers[WasmEdgesDetectionResultFlags.Found] = time;
        if (time - this.timers[v] > TIME_TO_FEEDBACK || this.feedback === WasmEdgesDetectionResultFlags.NotFound) {
          this.feedback = v;
        }
      } else {
        this.timers[v] = time;
      }
    });

    // no feedback if just Found and nothing else
    if (edgesDetectionResultFlags === WasmEdgesDetectionResultFlags.Found) {
      this.timers[WasmEdgesDetectionResultFlags.Found] = time;
      this.feedback = WasmEdgesDetectionResultFlags.Found;
    }
  };
}
