import React from 'react';
import './captureOverlay.scss';
import { STRINGS } from 'client/src/services/localization/strings';
import { Closer } from './Closer';
import { Searching } from './Searching';
import { Dirty } from './Dirty';
import CaptureFrame from 'client/src/components/CaptureView/CaptureFrame';

const FeedbackViews = {
  [STRINGS.camera_alert_closer]: Closer,
  [STRINGS.camera_alert_searching]: Searching,
  [STRINGS.camera_alert_sharpness_filter]: Dirty,
};

interface ICaptureOverlayProps {
  text: string;
  isRender: boolean;
  moveFrameClass: string;
  isFrameVisible: boolean;
  enableAutoCapture: boolean;
}

const CaptureOverlay = (props: ICaptureOverlayProps) => {
  const { text, isRender, moveFrameClass, isFrameVisible, enableAutoCapture } = props;
  const FeedbackView = FeedbackViews[text];

  return (
    <div className="online_capture-document">
      {isFrameVisible && enableAutoCapture ? <CaptureFrame moveClass={moveFrameClass} /> : null}
      {isRender ? null : FeedbackView ? (
        <FeedbackView />
      ) : (
        <div className="online_capture-document_title" data-testid="capture-overlay-message">
          {text}
        </div>
      )}
    </div>
  );
};

export default CaptureOverlay;
