/**
 * possible action types, that can be send to web worker
 */
enum WASM_ACTION_TYPES {
  OK = 'OK',
  ERROR = 'ERROR',
  LOAD_WASM = 'LOAD_WASM',
  CHECK_LICENSE = 'CHECK_LICENSE',
  DETECT_EDGES = 'DETECT_EDGES',
  DETECT_EDGES_AND_CROP = 'DETECT_EDGES_AND_CROP',
  CHECK_QUALITY_OCR = 'CHECK_QUALITY_OCR',
  RECROP_IMAGE = 'RECROP_IMAGE',
  START_IMAGE_DETECTION = 'START_IMAGE_DETECTION',
  STOP_IMAGE_DETECTION = 'STOP_IMAGE_DETECTION',
  CAPTURE_NEXT_FRAME = 'CAPTURE_NEXT_FRAME',
  SET_DOCUMENT_CONFIG = 'SET_CONFIG',
}

export default WASM_ACTION_TYPES;
