import React from 'react';
import { connect } from 'react-redux';

import './bottomPanel.scss';
import { APP_MODES, IAppStoreState } from 'client/src/types/declaration';
import { changeMode, saveData } from 'client/src/app/actions/actionCreators';
import { ActionCreator, AnyAction } from 'redux';
import { appSettings } from 'client/src/services/settings';
import { STRINGS } from 'client/src/services/localization/strings';

interface IBottomPanelStateProps {
  mode: APP_MODES;
  canAddNewImage: boolean;
}

interface IBottomPanelDispatchProps {
  changeMode: ActionCreator<AnyAction>;
  saveData: ActionCreator<AnyAction>;
}

class BottomPanel extends React.Component<IBottomPanelStateProps & IBottomPanelDispatchProps> {
  private _addNewImage = () => {
    this.props.changeMode('capture');
  };

  public render() {
    return (
      <div className="ABBYY_Capture_Module_BottomPanel">
        <button
          className="ABBYY_Capture_Module_BottomPanel_button"
          onClick={this._addNewImage}
          disabled={this.props.canAddNewImage}
          data-testid="preview-add"
        >
          {STRINGS.add_page}
        </button>
        <button
          className="ABBYY_Capture_Module_BottomPanel_button ABBYY_Capture_Module_BottomPanel_button--save"
          onClick={this.props.saveData}
          data-testid="preview-save"
        >
          {STRINGS.done}
        </button>
      </div>
    );
  }
}

export default connect(
  (state: IAppStoreState) => ({
    mode: state.currentMode,
    canAddNewImage: appSettings.userOptions.maxImagesCount <= state.images.length,
  }),
  {
    changeMode,
    saveData,
  }
)(BottomPanel);
