import React, { ChangeEvent, createRef, RefObject } from 'react';

import './captureBottomPanel.scss';

import { ICaptureResult } from 'client/src/types/declaration';
import wasmAdapter from 'client/src/services/wasm/wasmAdapter';

interface ICaptureBottomPanelProps {
  stopCapture: () => void;
  captureCurrent: () => void;
  onGetImage: (result: ICaptureResult) => void;
  beforeGetImage: () => Promise<void>;
  onFileSelectButtonClick: () => void;
  disableCapture: boolean;
  showFileLoadButton: boolean;
  onError: () => void;
  galleryInputRef: RefObject<HTMLInputElement>;
  currentCameraIndex: number;
  maxCameras: number;
  changeCamera: () => void;
  cameraError: boolean;
}

interface ICaptureBottomPanelState {
  manualCaptureDisabled: boolean;
  cameraClicked: boolean; // flag to not run camera animation on component load, only by click
}

class CaptureBottomPanel extends React.Component<ICaptureBottomPanelProps, ICaptureBottomPanelState> {
  private buttonRef = createRef<HTMLDivElement>();
  private fileButtonRef = createRef<HTMLDivElement>();

  public state = {
    manualCaptureDisabled: true,
    cameraClicked: false,
  };

  public componentDidMount(): void {
    this.setState({
      manualCaptureDisabled: false,
    });
  }

  private _handleFile = (file: File) => {
    return new Promise((resolve) => {
      const reader: FileReader = new FileReader();

      reader.onload = () => {
        const dataURL = reader.result as string;

        const img: HTMLImageElement = new Image();

        // draw on canvas and get result
        img.onload = async () => {
          try {
            const result = await wasmAdapter.getResultFromSource(img);

            resolve(result);
          } catch (e) {
            this.props.onError();
          }
        };

        img.onerror = () => {
          this.props.onError();
        };

        img.src = dataURL;
      };

      reader.readAsDataURL(file);
    });
  };

  private _onFileSelected = async (ev: ChangeEvent<HTMLInputElement>) => {
    ev.persist();

    await this.props.beforeGetImage();

    const file = ev.target.files[0];
    const result: ICaptureResult = (await this._handleFile(file)) as ICaptureResult;

    this.props.onGetImage(result);
  };

  private _onManualCaptureClick = () => {
    this.props.beforeGetImage().then(() => {
      this.setState({ manualCaptureDisabled: true });
      this.props.captureCurrent();
    });
  };

  private _onGetFileClick = () => {
    this.props.galleryInputRef.current.click();
    this.props.onFileSelectButtonClick();
  };

  private _changeCamera = () => {
    this.setState({
      cameraClicked: true,
    });
    this.props.changeCamera();
  };

  private _renderCameras = () => {
    let containerAdditionalClass = '';

    // skip animation on load, then show
    if (this.state.cameraClicked) {
      containerAdditionalClass = ' pos' + (this.props.currentCameraIndex % 2);
    }

    return (
      <div className={'ABBYY_Capture_Module_CaptureView_CameraPointContainer' + containerAdditionalClass}>
        {[...new Array(this.props.maxCameras)].map((v, i) => (
          <div key={i} className="ABBYY_Capture_Module_CaptureView_CameraPoint" />
        ))}
      </div>
    );
  };

  public render() {
    let captureButtonClass: string = 'ABBYY_Capture_Module_CaptureView__Capture_Button';
    let captureCameraIconClass: string = 'ABBYY_Capture_Module_CaptureView_CameraIcon';

    if (this.state.manualCaptureDisabled || this.props.disableCapture) {
      captureButtonClass = captureButtonClass + ' ABBYY_Capture_Module_CaptureView__Capture_Button--disabled';
      captureCameraIconClass = captureCameraIconClass + ' yellow';
    }

    const cameraContainerClass =
      'ABBYY_Capture_Module_CaptureView_CameraContainer ' +
      // tslint:disable-next-line:no-magic-numbers
      (this.props.maxCameras > 3 ? 'cameras_multiple' : 'cameras_max_3');

    return (
      <div className="ABBYY_Capture_Module_CaptureView_ButtonContainer">
        <div
          data-testid="capture-button-close"
          className="ABBYY_Capture_Module_CaptureView_Button ABBYY_Capture_Module_CaptureView_Button__Close_Button"
          onClick={this.props.stopCapture}
        />
        {this.props.showFileLoadButton && (
          <div className="ABBYY_Capture_Module_CaptureView_CentralContainer" data-testid="capture-button-camera">
            {!this.props.cameraError && this.props.maxCameras > 1 && (
              <div className={cameraContainerClass} onClick={this._changeCamera}>
                {this._renderCameras()}
                <div className={captureCameraIconClass} data-position={this.props.currentCameraIndex} />
              </div>
            )}
            {!this.props.cameraError && (
              <div
                data-testid="capture-button-manual"
                ref={this.buttonRef}
                className={captureButtonClass}
                onClick={this._onManualCaptureClick}
              />
            )}
          </div>
        )}
        {this.props.showFileLoadButton && (
          <input
            type="file"
            accept="image/jpeg,image/png,image/gif"
            onChange={this._onFileSelected}
            ref={this.props.galleryInputRef}
          />
        )}
        {this.props.showFileLoadButton && (
          <div
            data-testid="capture-button-gallery"
            ref={this.fileButtonRef}
            className="ABBYY_Capture_Module_CaptureView_Button ABBYY_Capture_Module_CaptureView_Button__Get_File_Button"
            onClick={this._onGetFileClick}
          />
        )}
      </div>
    );
  }
}

export default CaptureBottomPanel;
