// external API options
import { setLocalizedStrings } from 'client/src/services/localization/strings';
import { DOCUMENT_SIZE } from 'client/src/constants/documentSize';
import isIos from 'client/src/utils/isIos';
import { PreferredCameraResolution, defaultAppSettings } from './defaultSettings';
import cloneDeep from 'lodash/cloneDeep';
import { ITSettings, IWebCaptureDevOptions } from './index.d';

export { PreferredCameraResolution } from './defaultSettings';

export const appSettings: ITSettings = cloneDeep(defaultAppSettings);

export const setAppOptions = (newOptions: Partial<IWebCaptureDevOptions>) => {
  const { localizedStrings, ...options } = newOptions;

  if (localizedStrings) {
    setLocalizedStrings(localizedStrings);
  }

  setAspectRatio(options);
  setPreferredCameraResolution(options);

  appSettings.userOptions = {
    ...appSettings.userOptions,
    ...options,
  };
};

/**
 * set preferredCameraResolution from userOptions
 * @param options
 */
const setPreferredCameraResolution = (options: IWebCaptureDevOptions) => {
  switch (options.preferredCameraResolution) {
    case PreferredCameraResolution.ResolutionFullHD: // FullHD 1920 x 1080
      setCameraConstraints(1920, 1080);
      break;
    case PreferredCameraResolution.Resolution4K: // 4K 3840 x 2160
      setCameraConstraints(3840, 2160);
      break;
    case PreferredCameraResolution.ResolutionAuto: // Auto
    case undefined: // Auto (default)
      isIos ? setCameraConstraints(3840, 2160) : setCameraConstraints(1920, 1080);
      break;
  }
};

/**
 * set cameraConstraints from defaultAppSettings
 * @param width
 * @param height
 */
const setCameraConstraints = (width: number, height: number): void => {
  (appSettings.cameraConstraints.width as ConstrainULongRange).ideal = width;
  (appSettings.cameraConstraints.height as ConstrainULongRange).ideal = height;
};

/**
 * set aspectRatioMin and aspectRatioMax from userOptions
 * @param options
 */
const setAspectRatio = (options: IWebCaptureDevOptions) => {
  if (options.aspectRatioMin && !options.aspectRatioMax) {
    options.aspectRatioMax = Number.MAX_SAFE_INTEGER;
  }
  if (
    typeof options.aspectRatioMax === 'number' &&
    typeof options.aspectRatioMin === 'undefined' &&
    options.aspectRatioMax >= 1
  ) {
    options.aspectRatioMin = 1;
    options.aspectRatioMax = Math.max(1, options.aspectRatioMax);
  }

  // aspectRatio has priority, if not - then take preset from documentSize
  if (options.documentSize && !options.aspectRatioMin && DOCUMENT_SIZE.hasOwnProperty(options.documentSize)) {
    const documentRatio = DOCUMENT_SIZE[options.documentSize].width / DOCUMENT_SIZE[options.documentSize].height;

    options.aspectRatioMin = documentRatio;
    options.aspectRatioMax = documentRatio;
  } else {
    appSettings.userOptions.documentSize = null;
  }
};
