import React from 'react';
import { connect } from 'react-redux';
import { IAppStoreState } from 'client/src/types/declaration';
import { STRINGS } from 'client/src/services/localization/strings';

interface IPageNumber {
  current: number;
  max: number;
}

const PageNumber = ({ current, max }: IPageNumber) => {
  return (
    <>
      {STRINGS.page} {current + 1}
      {max > 1 ? ` ${STRINGS.of} ` + max : ''}
    </>
  );
};

export default connect((state: IAppStoreState) => ({
  current: state.currentImageIndex,
  max: state.images.length,
}))(PageNumber);
