import { IPoint } from 'client/src/types';

/**
 * get coords from user event
 * @param ev
 * @param isMobile
 */
export const getCoordsFromEvent = (ev: Event) => {
  if (ev instanceof TouchEvent) {
    return {
      x: ev.touches[0].clientX,
      y: ev.touches[0].clientY,
    };
  } else if (ev instanceof MouseEvent) {
    return {
      x: ev.clientX,
      y: ev.clientY,
    };
  }
};

/**
 * find nearest point to eventPos
 * @param rect
 * @param cropPoints
 * @param eventPos
 */
export const findNearestPointIndex = (rect: DOMRect, cropPoints: IPoint[], eventPos: IPoint) => {
  const relativeStartPos = {
    x: eventPos.x - rect.left,
    y: eventPos.y - rect.top,
  };

  // if point outside target rect - do nothing
  if (
    relativeStartPos.x < 0 ||
    relativeStartPos.y < 0 ||
    relativeStartPos.x > rect.width ||
    relativeStartPos.y > rect.height
  ) {
    return null;
  }

  return cropPoints.reduce(
    ([nearestIndex, maxDistance], point, i) => {
      const distanceToPoint =
        (point.x - relativeStartPos.x) * (point.x - relativeStartPos.x) +
        (point.y - relativeStartPos.y) * (point.y - relativeStartPos.y);

      if (distanceToPoint < maxDistance) {
        return [i, distanceToPoint];
      }

      return [nearestIndex, maxDistance];
    },
    [-1, Number.MAX_SAFE_INTEGER]
  )[0];
};
