import { appSettings } from 'client/src/services/settings';
import { STRINGS } from 'client/src/services/localization/strings';
import isIos from 'client/src/utils/isIos';
import MobileDetect from 'mobile-detect';

interface ICameraService {
  startStreamToVideo: (camera: MediaDeviceInfo, videoEl: HTMLVideoElement) => Promise<MediaStream>;
  stopStream: () => void;
  getCameras: () => Promise<MediaDeviceInfo[]>;
}

interface IMediaConstraints {
  audio: boolean;
  video: {
    width: { exact: number };
    height: { exact: number };
    deviceId: { exact: string };
  };
}

/**
 * filter cameras by resolution & facing mode
 */
const checkCameraCapabilities = (device: InputDeviceInfo) => {
  // simple way, chrome support device.getCapabilities method (Safari not)
  if (device.getCapabilities && navigator.userAgent.indexOf('NAVER') === -1) {
    const capabilities = device.getCapabilities();

    return (
      String(capabilities.facingMode) === '' ||
      String(capabilities.facingMode).indexOf('environment') > -1 ||
      (String(capabilities.facingMode) === 'user' && device.label.indexOf('front') === -1)
    );
  }

  // filter by device label, front /Передней панели
  return device.label.indexOf('передней панели') === -1 && device.label.indexOf('Front Camera') === -1;
};

/**
 * wait for stream starts on videoEl
 * @param stream
 * @param videoEl
 * @return {Promise}
 */
const awaitForVideo = (stream: MediaStream, videoEl: HTMLVideoElement) => {
  return new Promise((resolve, reject) => {
    const check = () => {
      if (videoEl.videoWidth !== 0) {
        if (!stream.active) {
          reject(STRINGS.camera_error);
        }

        resolve();
      } else {
        requestAnimationFrame(check);
      }
    };

    check();
  });
};

/**
 * different checks for device/OS combination requirements
 */
const preCheck = async () => {
  return new Promise((resolve, reject) => {
    const deviceInfo = new MobileDetect(window.navigator.userAgent);

    // turn off through settings
    if (!appSettings.userOptions.enableCamera) {
      reject(STRINGS.camera_disabled);
      return;
    }

    // IOS version < 12.2 not supported
    // tslint:disable-next-line:no-magic-numbers
    if (isIos && Number(deviceInfo.version('iOS')) <= 12.2) {
      reject(STRINGS.camera_low_res_ios);
      return;
    }

    // EDGE not supported
    if (/Edge/.test(navigator.userAgent)) {
      reject(STRINGS.browser_edge_error);
      return;
    }

    if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
      // on iOS all non-Safari browsers don't have access to userMedia API
      if (isIos && deviceInfo.userAgent() !== 'Safari') {
        // iOS & !Safari = no getUserMedia API
        reject(STRINGS.ios_non_safari_error);
        return;
      }

      // user reject permission for camera
      reject(STRINGS.camera_error);
      return;
    }

    resolve();
  });
};

/**
 * Gets new arr cameras features adjusted for minCameraResolution
 * @param cameras
 * @return ICamerasProps
 */
const getArrPropsCameras = async (cameras: MediaDeviceInfo[]): Promise<MediaDeviceInfo[]> => {
  const camerasNewArr: MediaDeviceInfo[] = [];

  for (const camera of cameras) {
    if (await testResolutions(camera.deviceId)) {
      camerasNewArr.push(camera);
    }
  }

  if (camerasNewArr.length === 0) {
    throw new Error(STRINGS.camera_low_res);
  }

  return camerasNewArr;
};

/**
 * Matches minCameraResolution with existing camera permissions
 * @param minCameraResolution
 * @return resolution
 */
const getCameraResolution = (minCameraResolution: number) => {
  let width: number;
  let height: number;

  switch (true) {
    case minCameraResolution <= 600: // SVGA 800 x 600
      width = 800;
      height = 600;
      break;
    case minCameraResolution <= 720: // HD 1280 x 720
      width = 1280;
      height = 720;
      break;
    case minCameraResolution <= 1080: // FullHD 1920 x 1080
      width = 1920;
      height = 1080;
      break;
    case minCameraResolution <= 1440: // 2K 2560 x 1440
      width = 2560;
      height = 1440;
      break;
    case minCameraResolution <= 2160: // 4K 3840 x 2160
      width = 3840;
      height = 2160;
      break;
    default:
      // 5K 5120 x 2880
      width = 5120;
      height = 2880;
  }

  return { width, height };
};

/**
 * Testing whether the camera supports the specified resolution (minCameraResolution) or not.
 * @param camera
 * @return boolean
 */
const testResolutions = async (camera: string): Promise<boolean> => {
  const constraints: IMediaConstraints = {
    audio: false,
    video: {
      width: {
        exact: getCameraResolution(appSettings.userOptions.minCameraResolution).width,
      },
      height: {
        exact: getCameraResolution(appSettings.userOptions.minCameraResolution).height,
      },
      deviceId: {
        exact: camera,
      },
    },
  };

  // We take a picture with the camera and determine its resolution.
  return await navigator.mediaDevices
    .getUserMedia(constraints)
    .then((stream) => {
      stream.getTracks().forEach((track) => track.stop());

      return true;
    })
    .catch(() => false);
};

let currentStream: MediaStream;

const cameraService: ICameraService = {
  getCameras: async () => {
    try {
      // check device/browser capabilities
      await preCheck();

      // on first check, if we don't ask for permissions, enumerateDevices return empty objects
      // so first we ask for camera permission, then stop stream and after we can get detailed cameras info
      // so we can filter out front cameras from list
      // without this, getCapabilities() on other pages does not work
      await navigator.mediaDevices.getUserMedia({ video: appSettings.cameraConstraints }).then((stream) => {
        stream.getTracks().forEach((track) => track.stop());
      });

      const cameras = await navigator.mediaDevices
        .enumerateDevices()
        .then((devices) =>
          (devices as InputDeviceInfo[]).filter(
            (device) => device.kind === 'videoinput' && checkCameraCapabilities(device)
          )
        );

      return getArrPropsCameras(cameras);
    } catch (e) {
      if (typeof e === 'string') {
        throw new Error(e);
      }

      throw new Error(STRINGS.camera_error);
    }
  },

  startStreamToVideo: (camera, videoEl) => {
    if (currentStream) {
      cameraService.stopStream();
    }

    return new Promise((resolve, reject) => {
      if (!camera) {
        reject(STRINGS.camera_error);
      }

      // check capabilities
      preCheck()
        .then(() => {
          return navigator.mediaDevices.getUserMedia({
            video: {
              ...appSettings.cameraConstraints,
              deviceId: camera.deviceId,
            },
          });
        })
        .then((stream) => {
          // store & attach to videoEl
          currentStream = stream;
          videoEl.srcObject = currentStream;
        })
        .then(() => {
          // wait for video to start playing
          return awaitForVideo(currentStream, videoEl);
        })
        .then(resolve)
        .catch((error: Error) => {
          if (typeof error === 'string') {
            reject(error);
          }

          reject(STRINGS.camera_error);
        });
    });
  },
  stopStream: () => {
    if (currentStream) {
      currentStream.getTracks().forEach((track) => {
        track.stop();
      });
    }
  },
};

export default cameraService;
