import { appSettings } from 'client/src/services/settings';
import { IPoint } from 'client/src/types';

/**
 * get scaled dimensions
 * live capture differs from visual part to increase performance (minDimension x adjustedOtherDimension)
 * @param width
 * @param height
 */
export const getScaledDimensions = (width: number, height: number) => {
  const scale = appSettings.capture.minDimension / Math.min(width, height);

  return {
    scale,
    scaledWidth: Math.floor(scale * width),
    scaledHeight: Math.floor(scale * height),
  };
};

/**
 * transform array [x,y,...] to [{x,y},...]
 * @param vertices
 */
export const arrayToPoints = (vertices: Int32Array) => {
  const result: Array<{ x: number; y: number }> = [];

  for (let i = 0; i < vertices.length; i += 2) {
    result.push({
      x: vertices[i],
      y: vertices[i + 1],
    });
  }
  return result;
};

/**
 * transform array [{x,y},...] to [x,y,...]
 * @param vertices
 */
export const pointsToArray = (vertices: IPoint[]): Int32Array => {
  const result: number[] = [];

  vertices.forEach((vertice) => {
    result.push(vertice.x, vertice.y);
  });
  return new Int32Array(result);
};
