import React from 'react';

import './app.scss';
import { APP_MODES, IAppStoreState } from 'client/src/types/declaration';
import ImagePreview from '../ImagePreview';
import CaptureView from '../CaptureView';
import CropView from '../CropView';
import { connect } from 'react-redux';
import MobileDetect from 'mobile-detect';

const Views: Record<APP_MODES, any> = {
  preview: ImagePreview,
  recapture: CaptureView,
  capture: CaptureView,
  crop: CropView,
};

interface IWorkingPanelProps {
  mode: APP_MODES;
}

interface IWorkingPanelState {
  deviceType: string;
  orientation: string;
}

class WorkingPanel extends React.Component<IWorkingPanelProps, IWorkingPanelState> {
  constructor(props: IWorkingPanelProps) {
    super(props);

    const mobileDetect = new MobileDetect(navigator.userAgent);

    this.state = {
      deviceType: mobileDetect.mobile() ? (mobileDetect.tablet() ? 'tablet' : 'mobile') : 'desktop',
      orientation: 'portrait',
    };
  }

  private onOrientationChange = () => {
    // since on this event window width&height not resized yet
    const afterOrientationChange = async () => {
      this.setState({
        orientation: await this.getOrientation(),
      });

      window.removeEventListener('resize', afterOrientationChange);
    };

    window.addEventListener('resize', afterOrientationChange);
  };

  private getOrientation = async () => {
    await new Promise((resolve) => setTimeout(resolve, 500));

    return window.innerWidth > window.innerHeight ? 'landscape' : 'portrait';
  };

  public async componentDidMount() {
    window.addEventListener('orientationchange', this.onOrientationChange);

    this.setState({
      orientation: await this.getOrientation(),
    });
  }

  public componentWillUnmount(): void {
    window.removeEventListener('orientationchange', this.onOrientationChange);
  }

  public render() {
    const CurrentView = Views[this.props.mode];

    return (
      <div className={`ABBYY_Capture_Module__Container ${this.state.deviceType} ${this.state.orientation}`}>
        <CurrentView />
      </div>
    );
  }
}

export default connect((state: IAppStoreState) => ({
  mode: state.currentMode,
}))(WorkingPanel);
