import { appSettings } from 'client/src/services/settings';
import { IWasmAdapter } from 'client/src/types/wasmAdapter';
import captureProcess from './captureProcess';
import { pointsToArray } from 'client/src/services/wasm/utils';
import CanvasHelper from 'client/src/services/wasm/canvasHelper';
import { getWasmWorker } from 'client/src/services/wasm-worker/wasm-worker-adapter';
import WASM_ACTION_TYPES from 'client/src/services/wasm-worker/WASM_ACTION_TYPES';
import { CDocumentHints } from '../wasm-worker/documentHints';
import { DOCUMENT_SIZE } from 'client/src/constants/documentSize';

/** high level api for Wasm, aware of user config
 * all interaction with wasm encapsulated here
 */
const wasmAdapter: IWasmAdapter = {
  /**
   * initialize loading wasm and wait for wasm initialization
   * @param version
   */
  loadWasmScripts: async (version = '') => {
    return await getWasmWorker().postMessage({
      type: WASM_ACTION_TYPES.LOAD_WASM,
      version,
      path: appSettings.userOptions.wasmFilesPath,
    });
  },

  /**
   * load license and check it for validness
   * @param licenseFilePath
   */
  checkLicense: async (licenseFilePath: string) => {
    await getWasmWorker().postMessage({
      type: WASM_ACTION_TYPES.CHECK_LICENSE,
      licenseFilePath,
    });
  },

  /**
   *
   */
  setWasmDocumentConfig: async () => {
    const { aspectRatioMin, aspectRatioMax, documentSize } = appSettings.userOptions;

    const documentHints = new CDocumentHints({
      aspectRatioMin,
      aspectRatioMax,
      documentSize: DOCUMENT_SIZE[documentSize],
    });

    return getWasmWorker().postMessage({
      documentHints,
      type: WASM_ACTION_TYPES.SET_DOCUMENT_CONFIG,
    });
  },

  /**
   * check if image suitable for OCR
   * @param jpegData
   * @param documentBoundary
   */
  checkQualityAssessmentForOCR: async (jpegData, documentBoundary) => {
    if (!appSettings.userOptions.enableQualityAssessmentForOCR) {
      return null;
    }

    const jpegDataCopy = jpegData.slice(0); // wasm webworker nullify buffer after processing

    return getWasmWorker()
      .postMessage(
        {
          jpegData: jpegDataCopy, // because after worker ends with data, it clears
          documentBoundary,
          type: WASM_ACTION_TYPES.CHECK_QUALITY_OCR,
        },
        [jpegDataCopy.buffer]
      )
      .then((message) => message.result);
  },
  /**
   * recrop image with new edges
   * @param jpegData
   * @param documentBoundary
   * @param imageQuality
   */
  recropImage: async (jpegData, documentBoundary, imageQuality = appSettings.userOptions.jpegQuality) => {
    const jpegDataCopy = jpegData.slice(0); // wasm webworker nullify buffer after processing

    return getWasmWorker()
      .postMessage(
        {
          jpegData: jpegDataCopy,
          documentBoundary: pointsToArray(documentBoundary),
          imageQuality,
          type: WASM_ACTION_TYPES.RECROP_IMAGE,
        },
        [jpegDataCopy.buffer]
      )
      .then((message) => message.result)
      .catch(() => {
        return null;
      });
  },

  /**
   * main entry point for all NEW images not from live captureProcess
   * @param source
   * @param width
   * @param height
   * @param areaOfInterest
   */
  getResultFromSource: async (source, width, height, areaOfInterest) => {
    const sourceWidth = width || Number(source.width);
    const sourceHeight = height || Number(source.height);
    const areaOfInt = areaOfInterest || {
      x: 0,
      y: 0,
      width: sourceWidth,
      height: sourceHeight,
    };

    const canvasHelper = new CanvasHelper(areaOfInt.width, areaOfInt.height);

    canvasHelper.drawFrame(source, [-areaOfInt.x, -areaOfInt.y, sourceWidth, sourceHeight]);

    const sourceJpegData = canvasHelper.getJpegData();
    const croppedResult = await wasmAdapter.detectEdgesAndCrop(
      canvasHelper,
      areaOfInt.width,
      areaOfInt.height,
      appSettings.userOptions.jpegQuality
    );

    const ocrQuality = await wasmAdapter.checkQualityAssessmentForOCR(sourceJpegData, croppedResult.edges);

    return {
      originalImage: sourceJpegData,
      documentBoundary: croppedResult.edges,
      originalDocumentBoundary: [...croppedResult.edges],
      capturedImage: croppedResult.data,
      ocrQuality,
    };
  },

  /**
   * main entry point for finding edges on static images, used in getResultFromSource
   * @param canvasHelper
   * @param imageWidth
   * @param imageHeight
   * @param imageQuality
   */
  detectEdgesAndCrop: async (
    canvasHelper: CanvasHelper,
    imageWidth,
    imageHeight,
    imageQuality = appSettings.userOptions.jpegQuality
  ) => {
    const bmpData = canvasHelper.getBMPData();

    if (appSettings.userOptions.enableAutoCrop) {
      return getWasmWorker()
        .postMessage(
          {
            bmpData: bmpData.data,
            imageWidth,
            imageHeight,
            imageQuality,
            type: WASM_ACTION_TYPES.DETECT_EDGES_AND_CROP,
          },
          [bmpData.data.buffer]
        )
        .then((message) => ({
          ...message.result,
          edges: message.result.edges,
        }));
    } else {
      // no crop, only edges
      const edges = await getWasmWorker()
        .postMessage(
          {
            bmpData: bmpData.data,
            imageWidth,
            imageHeight,
            type: WASM_ACTION_TYPES.DETECT_EDGES,
          },
          [bmpData.data.buffer]
        )
        .then((message) => message.result.edges);

      return {
        data: canvasHelper.getJpegData(),
        edges,
      };
    }
  },

  /**
   * live capture process
   */
  captureProcess: {
    /**
     * @param videoEl - videoEl, for getting image data frame by frame
     * @param areaOfInterest - specify areaOfInterest for wasm to work within
     * @param onGetImage - callback, when we get final captured image
     * @param onNextFrame - callback, triggers each frame, for show edges & feedback
     * @param onStartHandleProcess - wasm found best image and start processing (can take some time on slow devices)
     */
    startCaptureProcess: async ({ videoEl, areaOfInterest, onGetImage, onNextFrame, onStartHandleProcess }) => {
      if (!appSettings.userOptions.enableAutoCapture) {
        return;
      }

      await captureProcess.start({
        videoEl,
        areaOfInterest,
        frameBoundaryPadding: appSettings.userOptions.frameBoundaryPadding,
        onNextFrame,
        onBestDocumentGet: async (originalImage, bestEdges) => {
          onStartHandleProcess();

          const result = appSettings.userOptions.enableAutoCrop
            ? await wasmAdapter.recropImage(originalImage, bestEdges)
            : originalImage;

          onGetImage({
            originalImage,
            documentBoundary: bestEdges,
            originalDocumentBoundary: [...bestEdges],
            capturedImage: result,
          });
        },
      });
    },

    stopCaptureProcess: async () => {
      await captureProcess.stop();
    },
  },
};

export default wasmAdapter;
