import { IWebCaptureDevOptions } from './settings/index.d';

interface IRuleValidUserOptions {
  aspectRatioMin: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  aspectRatioMax: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  jpegQuality: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  startPage: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  maxImagesCount: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  enableCamera: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  enableAutoCapture: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  enableAutoCrop: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  enableQualityAssessmentForOCR: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  licenseFilePath: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  wasmFilesPath: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  frameBoundaryPadding: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  minCameraResolution: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  documentSize: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  preferredCameraResolution: (newOptions: Partial<IWebCaptureDevOptions>) => string;
  enableSharpnessFilter: (newOptions: Partial<IWebCaptureDevOptions>) => string;
}

const ruleValidUserOptions: IRuleValidUserOptions = {
  aspectRatioMin: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.aspectRatioMin !== 'number') {
      return `Invalid value of parameter aspectRatioMin (${newOptions.aspectRatioMin}). Parameter type should be number.`;
    } else if (newOptions.aspectRatioMin > Number.MAX_SAFE_INTEGER) {
      return `The value of parameter aspectRatioMin (${newOptions.aspectRatioMin}) exceeds maximum safe integer. Please, choose smaller value.`;
    } else if (isAspectRatioMinMoreThanOne(newOptions)) {
      return `Invalid value of parameter aspectRatioMin (${newOptions.aspectRatioMin}). The value should be >= 1.`;
    } else if (newOptions.aspectRatioMax < newOptions.aspectRatioMin && newOptions.aspectRatioMin !== 0) {
      return `Invalid parameter values aspectRatioMin (${newOptions.aspectRatioMin}) and aspectRatioMax(${newOptions.aspectRatioMax}). The value aspectRatioMin should be <= the value aspectRatioMax.`;
    } else if (isNaN(newOptions.aspectRatioMin)) {
      return `Invalid value of parameter aspectRatioMin (${newOptions.aspectRatioMin}). The value should be !== NaN.`;
    }

    return '';
  },
  aspectRatioMax: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.aspectRatioMax !== 'number') {
      return `Invalid value of parameter aspectRatioMax (${newOptions.aspectRatioMax}). Parameter type should be number.`;
    } else if (newOptions.aspectRatioMax > Number.MAX_SAFE_INTEGER) {
      return `The value of parameter aspectRatioMax (${newOptions.aspectRatioMax}) exceeds maximum safe integer. Please, choose smaller value.`;
    } else if (newOptions.aspectRatioMax !== 0 && newOptions.aspectRatioMax < 1) {
      return `Invalid value of parameter aspectRatioMax (${newOptions.aspectRatioMax}). The value should be >= 1.`;
    } else if (isNaN(newOptions.aspectRatioMax)) {
      return `Invalid value of parameter aspectRatioMax (${newOptions.aspectRatioMax}). The value should be !== NaN.`;
    }

    return '';
  },
  jpegQuality: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (
      typeof newOptions.jpegQuality !== 'number' ||
      isNaN(newOptions.jpegQuality) ||
      newOptions.jpegQuality < 0 ||
      (newOptions.jpegQuality > 0 && newOptions.jpegQuality < 1) ||
      newOptions.jpegQuality > 100
    ) {
      return `Invalid value of jpegQuality (${newOptions.jpegQuality}). The value should be in the range from 0 to 100.`;
    }

    return '';
  },
  startPage: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.startPage !== 'number' || isNaN(newOptions.startPage)) {
      return `Invalid value of parameter startPage (${newOptions.startPage}). Parameter type should be number.`;
    }

    return '';
  },
  maxImagesCount: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.maxImagesCount !== 'number' || isNaN(newOptions.maxImagesCount)) {
      return `Invalid value of parameter maxImagesCount (${newOptions.maxImagesCount}). Parameter type should be number.`;
    } else if (newOptions.maxImagesCount < 1) {
      return `Invalid value of parameter maxImagesCount (${newOptions.maxImagesCount}). The value should be >= 1.`;
    } else if (newOptions.maxImagesCount > Number.MAX_SAFE_INTEGER) {
      return `The value of parameter maxImagesCount (${newOptions.maxImagesCount}) exceeds maximum safe integer. Please, choose smaller value.`;
    } else if (newOptions.maxImagesCount < newOptions.startPage) {
      return `Invalid parameter values maxImagesCount (${newOptions.maxImagesCount}) and startPage(${newOptions.startPage}). The value maxImagesCount should be >= the value startPage.`;
    }

    return '';
  },
  enableCamera: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.enableCamera !== 'boolean') {
      return `Invalid value of parameter enableCamera (${newOptions.enableCamera}). Parameter type should be boolean.`;
    }

    return '';
  },
  enableAutoCapture: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.enableAutoCapture !== 'boolean') {
      return `Invalid value of parameter enableAutoCapture (${newOptions.enableAutoCapture}). Parameter type should be boolean.`;
    }

    return '';
  },
  enableAutoCrop: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.enableAutoCrop !== 'boolean') {
      return `Invalid value of parameter enableAutoCrop (${newOptions.enableAutoCrop}). Parameter type should be boolean.`;
    }

    return '';
  },
  enableQualityAssessmentForOCR: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.enableQualityAssessmentForOCR !== 'boolean') {
      return `Invalid value of parameter enableQualityAssessmentForOCR (${newOptions.enableQualityAssessmentForOCR}). Parameter type should be boolean.`;
    }

    return '';
  },
  licenseFilePath: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.licenseFilePath !== 'string') {
      return `Invalid value of parameter licenseFilePath (${newOptions.licenseFilePath}). Parameter type should be string.`;
    }

    return '';
  },
  wasmFilesPath: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.wasmFilesPath !== 'string') {
      return `Invalid value of parameter wasmFilesPath (${newOptions.wasmFilesPath}). Parameter type should be string.`;
    }

    return '';
  },
  frameBoundaryPadding: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (
      typeof newOptions.frameBoundaryPadding !== 'number' ||
      isNaN(newOptions.frameBoundaryPadding) ||
      newOptions.frameBoundaryPadding < 1 ||
      newOptions.frameBoundaryPadding > 50
    ) {
      return `Invalid value of parameter frameBoundaryPadding (${newOptions.frameBoundaryPadding}). The value should be in the range from 1 to 50.`;
    }

    return '';
  },
  minCameraResolution: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (
      typeof newOptions.minCameraResolution !== 'number' ||
      isNaN(newOptions.minCameraResolution) ||
      newOptions.minCameraResolution < 0
    ) {
      return `Invalid value of parameter minCameraResolution (${newOptions.minCameraResolution}). The value should be >= 0.`;
    }

    return '';
  },
  documentSize: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (
      newOptions.documentSize !== null &&
      newOptions.documentSize !== 'A4' &&
      newOptions.documentSize !== 'letter' &&
      newOptions.documentSize !== 'ID-1' &&
      newOptions.documentSize !== 'ID-2' &&
      newOptions.documentSize !== 'ID-3'
    ) {
      return `Invalid value of parameter documentSize (${newOptions.documentSize}). The value must be one of the following: A4, letter, ID-1, ID-2, ID-3.`;
    } else if (typeof newOptions.documentSize !== 'string' && newOptions.documentSize !== null) {
      return `Invalid value of parameter documentSize (${newOptions.documentSize}). Parameter type should be string.`;
    }

    return '';
  },
  preferredCameraResolution: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (
      newOptions.preferredCameraResolution !== '4K' &&
      newOptions.preferredCameraResolution !== 'FullHD' &&
      newOptions.preferredCameraResolution !== 'Auto'
    ) {
      return `Invalid value of parameter preferredCameraResolution (${newOptions.preferredCameraResolution}). The value must be one of the following: 4K, FullHD, Auto.`;
    } else if (typeof newOptions.preferredCameraResolution !== 'string') {
      return `Invalid value of parameter preferredCameraResolution (${newOptions.preferredCameraResolution}). Parameter type should be string.`;
    }

    return '';
  },
  enableSharpnessFilter: (newOptions: Partial<IWebCaptureDevOptions>) => {
    if (typeof newOptions.enableSharpnessFilter !== 'boolean') {
      return `Invalid value of parameter enableSharpnessFilter (${newOptions.enableSharpnessFilter}). Parameter type should be boolean.`;
    }

    return '';
  },
};

const isAspectRatioMinMoreThanOne = (newOptions: Partial<IWebCaptureDevOptions>) => {
  return (
    (typeof newOptions.aspectRatioMax === 'number' &&
      !isNaN(newOptions.aspectRatioMax) &&
      newOptions.aspectRatioMax > 0 &&
      newOptions.aspectRatioMin < 1) ||
    newOptions.aspectRatioMin < 0
  );
};

const isUserOptionsValid = (newOptions: Partial<IWebCaptureDevOptions>) => {
  let isValid = true;

  Object.entries(ruleValidUserOptions).forEach((key) => {
    if (key[1](newOptions) !== '') {
      window.console.error(key[1](newOptions));

      isValid = false;
    }
  });

  return isValid;
};

export default isUserOptionsValid;
