import { IPoint } from 'client/src/types';
import { pointsToArray } from 'client/src/services/wasm/utils';

export const isEdgesFound = (edges: Int32Array) => {
  return edges.some((x) => x > 0);
};

/**
 * of edges is empty, return whole image as result
 * @param edges
 * @param width
 * @param height
 */
export const transformIfEmpty = (edges: IPoint[], width: number, height: number) => {
  if (isEdgesFound(pointsToArray(edges))) {
    return edges;
  }

  // from bottom left corner (wasm order)
  return [{ x: 0, y: height }, { x: 0, y: 0 }, { x: width, y: 0 }, { x: width, y: height }];
};
