import React, { createRef } from 'react';
import { connect } from 'react-redux';

import styles from './cropView.scss';
import { IPoint, TQualityAssessmentResult } from 'client/src/types';
import { IAppStoreState, ICaptureResult } from 'client/src/types/declaration';
import CropLayer from 'client/src/components/CropView/CropLayer';
import { changeMode, replaceImages } from 'client/src/app/actions/actionCreators';
import { ActionCreator, AnyAction } from 'redux';
import wasmAdapter from 'client/src/services/wasm/wasmAdapter';
import TopPanel from 'client/src/components/TopPanel';
import NavBar from 'client/src/components/NavBar';
import ProcessPage from 'client/src/components/ProcessPage';
import { STRINGS } from 'client/src/services/localization/strings';
import { transformIfEmpty } from 'client/src/utils/edges';
import _throttle from 'lodash/throttle';

// from scss module, to work correctly with svg drag layer
const svgExtraSpace = parseInt(styles.svgExtraSpace, 10);
const topPanelHeight = parseInt(styles.topPanelHeight, 10);

interface ICropViewStateProps {
  images: ICaptureResult[]; // all captures images
  imageBlob: string; // current selected image
  imageIndex: number; // index of current selected image
}

interface ICropViewDispatchProps {
  switchToPreview: ActionCreator<AnyAction>;
  replaceImages: ActionCreator<AnyAction>;
}

interface ICropViewState {
  cropInfos: Array<{
    // crop detail for each image, for bulk apply
    scale: number; // scale of image on screen
    cropPoints: IPoint[];
    imgWidth: number;
    imgHeight: number;
  }>;
  showProcessPage: boolean; // to show during wasm apply (can take some time)
}

/**
 * Container component for whole CropPage functionality & UI
 */
class CropView extends React.Component<ICropViewStateProps & ICropViewDispatchProps, ICropViewState> {
  private imgRef = createRef<HTMLImageElement>();
  private imageContainerRef = createRef<HTMLDivElement>();
  private cropWorkingPaneRef = createRef<HTMLDivElement>();
  private detectScaleTimeout: number;

  constructor(props: ICropViewStateProps & ICropViewDispatchProps) {
    super(props);

    this.state = {
      cropInfos: [...new Array(props.images.length)].map(() => ({
        scale: 1,
        cropPoints: [],
        imgWidth: 0, // for cache purpose, will be updated on image load
        imgHeight: 0, // for cache purpose, will be updated on image load
      })), // state here reflects svg for saving functionality
      showProcessPage: false,
    };
  }

  private _updatePoint = (index: number, newPos: IPoint) => {
    this.state.cropInfos[this.props.imageIndex].cropPoints[index] = newPos;

    this.setState({
      cropInfos: [...this.state.cropInfos],
    });
  };

  // on image load recalculate crop layout
  public componentDidMount(): void {
    this._setMaxHeight();

    window.addEventListener('resize', this._setMaxHeight);
  }

  public componentWillUnmount(): void {
    window.removeEventListener('resize', this._setMaxHeight);
  }

  public componentDidUpdate(
    prevProps: Readonly<ICropViewStateProps & ICropViewDispatchProps>,
    prevState: Readonly<ICropViewState>
  ): void {
    if (
      prevProps.imageBlob !== this.props.imageBlob &&
      this.state.cropInfos[this.props.imageIndex].cropPoints.length === 0
    ) {
      // clear memory
      URL.revokeObjectURL(prevProps.imageBlob);
      if (this.detectScaleTimeout) {
        clearTimeout(this.detectScaleTimeout);
      }

      // wait for image dimensions to be updated by browser
      this.detectScaleTimeout = window.setTimeout(this._detectScale, 100);
    }
  }

  /**
   * recalculate layout and image scale
   * @private
   */
  private _setMaxHeight = _throttle(() => {
    const cropWorkingPaneHeight = this.cropWorkingPaneRef.current.offsetHeight - topPanelHeight;

    if (cropWorkingPaneHeight) {
      this.imageContainerRef.current.style.maxHeight = cropWorkingPaneHeight - 2 * svgExtraSpace + 'px';
      this.imageContainerRef.current.style.visibility = 'visible';
      this._detectScale();
    } else {
      requestAnimationFrame(this._setMaxHeight);
    }
  }, 100);

  /**
   * detect current image scale (naturalDimension/ current)
   * @private
   */
  private _detectScale = () => {
    if (this.imgRef.current.width) {
      const image = this.imgRef.current;
      let cropPoints = this.props.images[this.props.imageIndex].documentBoundary;
      const scale = image.width / image.naturalWidth;

      // if all crop points the same -> crop points 0,0,width,height
      if (cropPoints[0].x === cropPoints[1].x && cropPoints[0].y === cropPoints[1].y) {
        cropPoints = [
          { x: 0, y: image.naturalHeight },
          { x: 0, y: 0 },
          { x: image.naturalWidth, y: 0 },
          { x: image.naturalWidth, y: image.naturalHeight },
        ];
      }

      this.state.cropInfos[this.props.imageIndex] = {
        scale,
        cropPoints: cropPoints.map((p) => ({
          x: p.x * scale,
          y: p.y * scale,
        })),
        imgWidth: image.naturalWidth,
        imgHeight: image.naturalHeight,
      };

      this.setState({
        cropInfos: [...this.state.cropInfos],
      });
    } else {
      requestAnimationFrame(this._detectScale);
    }
  };

  /**
   * bulk recrop all modified images
   * @private
   */
  private _reCrop = async () => {
    this.setState({
      showProcessPage: true,
    });

    try {
      const processedImages: Array<{ index: number; result: ICaptureResult }> = [];

      for await (const [index, cropInfo] of this.state.cropInfos.entries()) {
        try {
          await new Promise(async (resolve, reject) => {
            if (cropInfo.cropPoints.length === 0) {
              // image was not touched
              resolve();
            } else {
              const documentBoundary: IPoint[] = this.state.cropInfos[index].cropPoints.map((p) => ({
                x: p.x / this.state.cropInfos[index].scale,
                y: p.y / this.state.cropInfos[index].scale,
              }));

              const croppedJpegData = await wasmAdapter.recropImage(
                this.props.images[index].originalImage,
                documentBoundary
              );

              if (croppedJpegData) {
                // success
                const ocrQuality: TQualityAssessmentResult = await wasmAdapter.checkQualityAssessmentForOCR(
                  this.props.images[index].originalImage,
                  documentBoundary
                );

                processedImages.push({
                  index,
                  result: {
                    originalImage: this.props.images[index].originalImage,
                    capturedImage: croppedJpegData,
                    documentBoundary,
                    originalDocumentBoundary: this.props.images[index].originalDocumentBoundary,
                    ocrQuality,
                  },
                });

                resolve();
              } else {
                reject();
              }
            }
          });
          // tslint:disable-next-line:no-empty
        } catch (e) {}
      }

      this.props.replaceImages(processedImages);
    } finally {
      this.setState(
        {
          showProcessPage: false,
        },
        () => {
          this.props.switchToPreview();
        }
      );
    }
  };

  /**
   * set crop points to whole image
   */
  private _cropFull = () => {
    const cropInfos = [...this.state.cropInfos];
    const scaledWidth = cropInfos[this.props.imageIndex].scale * cropInfos[this.props.imageIndex].imgWidth;
    const scaledHeight = cropInfos[this.props.imageIndex].scale * cropInfos[this.props.imageIndex].imgHeight;

    cropInfos[this.props.imageIndex] = {
      ...this.state.cropInfos[this.props.imageIndex],
      cropPoints: [
        { x: 0, y: scaledHeight },
        { x: 0, y: 0 },
        { x: scaledWidth, y: 0 },
        { x: scaledWidth, y: scaledHeight },
      ],
    };

    this.setState({
      cropInfos,
    });
  };

  /**
   * reset points to original wasm value
   * @private
   */
  private _cropOriginal = () => {
    const cropInfos = [...this.state.cropInfos];
    const image = this.props.images[this.props.imageIndex];
    const cropInfo = cropInfos[this.props.imageIndex];

    cropInfo.cropPoints = transformIfEmpty(
      image.originalDocumentBoundary.map((p) => ({
        x: p.x * cropInfo.scale,
        y: p.y * cropInfo.scale,
      })),
      cropInfo.imgWidth * cropInfo.scale,
      cropInfo.imgHeight * cropInfo.scale
    );

    this.setState({
      cropInfos,
    });
  };

  public render() {
    const cropInfo = this.state.cropInfos[this.props.imageIndex];

    return (
      <div className="ABBYY_Capture_Module__CropView">
        <TopPanel>
          <span className="button normal" onClick={this.props.switchToPreview} data-testid="crop-cancel">
            {STRINGS.cancel}
          </span>
          <span>{STRINGS.crop}</span>
          <span className="button" onClick={this._reCrop} data-testid="crop-apply">
            {STRINGS.apply}
          </span>
        </TopPanel>
        <div className="ABBYY_Capture_Module__CropView_WorkingPane" ref={this.cropWorkingPaneRef}>
          <div ref={this.imageContainerRef}>
            <img ref={this.imgRef} src={this.props.imageBlob} />
            <CropLayer
              img={this.props.images[this.props.imageIndex].originalImage}
              imgWidth={cropInfo.scale * cropInfo.imgWidth}
              imgHeight={cropInfo.scale * cropInfo.imgHeight}
              cropPoints={cropInfo.cropPoints}
              updatePoint={this._updatePoint}
              svgExtraSpace={svgExtraSpace}
            />
          </div>
        </div>
        <div className="ABBYY_Capture_Module__CropView_BottomPane">
          <button
            className="ABBYY_Capture_Module__CropView_FullCrop"
            onClick={this._cropFull}
            data-testid="crop-full"
          />
          <NavBar />
          <button
            className="ABBYY_Capture_Module__CropView_OriginalCrop"
            onClick={this._cropOriginal}
            data-testid="crop-original"
          />
        </div>
        {this.state.showProcessPage && <ProcessPage message={STRINGS.image_processing} />}
      </div>
    );
  }
}

export default connect(
  (state: IAppStoreState) => ({
    images: state.images,
    imageIndex: state.currentImageIndex,
    imageBlob: window.URL.createObjectURL(
      new Blob([state.images[state.currentImageIndex].originalImage], { type: 'image/jpeg' })
    ),
  }),
  {
    switchToPreview: () => changeMode('preview'),
    replaceImages,
  }
)(CropView);
