/**
 * parse navigator.userAgent. Get information about OS version & browser
 */
const getBrowserInfo = () => {
  const ua = navigator.userAgent;
  let os = '';
  let osVersion = '';
  let temp;
  let M = ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
  const clientStrings = [
    { s: 'Android', r: /Android/ },
    { s: 'Linux', r: /(Linux|X11)/ },
    { s: 'iOS', r: /(iPhone|iPad|iPod)/ },
    { s: 'Mac OS X', r: /Mac OS X/ },
    { s: 'Mac OS', r: /(MacPPC|MacIntel|Mac_PowerPC|Macintosh)/ },
  ];

  // tslint:disable-next-line
  for (const id in clientStrings) {
    const cs = clientStrings[id];
    if (cs.r.test(ua)) {
      os = cs.s;
      break;
    }
  }

  switch (os) {
    case 'Mac OS X':
      osVersion = /Mac OS X (10[\.\_\d]+)/.exec(ua)[1];
      break;

    case 'Android':
      osVersion = /Android ([\.\_\d]+)/.exec(ua)[1];
      break;

    case 'iOS':
      const osVersionRes = /OS (\d+)_(\d+)_?(\d+)?/.exec(ua);
      // tslint:disable-next-line:no-magic-numbers
      osVersion = osVersionRes[1] + '.' + osVersionRes[2] + '.' + (osVersionRes[3] || 0);
      break;
  }

  if (/trident/i.test(M[1])) {
    temp = /\brv[ :]+(\d+)/g.exec(ua) || [];
    return {
      name: 'IE',
      version: temp[1] || '',
      os,
      osVersion,
    };
  }
  if (M[1] === 'Chrome') {
    temp = ua.match(/\b(OPR|Edge)\/(\d+)/);
    if (temp !== null) {
      return {
        name: temp[1].replace('OPR', 'Opera'),
        version: temp[2],
        os,
        osVersion,
      };
    }
  }
  M = M[2] ? [M[1], M[2]] : [navigator.appName, navigator.appVersion, '-?'];
  temp = ua.match(/version\/(\d+)/i);
  if (temp !== null) {
    M.splice(1, 1, temp[1]);
  }

  return {
    name: M[0],
    version: M[1],
    os,
    osVersion,
  };
};

export default getBrowserInfo;
