import { applyMiddleware, compose, createStore } from 'redux';
import reducer, { INITIAL_STATE } from 'client/src/app/reducers/reducer';
import onSaveMiddleware from 'client/src/app/middleware/onSave';
import onSwitchCamera from 'client/src/app/middleware/onSwitchCamera';

import { APP_MODES, ICaptureResult, IOnSave } from 'client/src/types/declaration';
import { appSettings } from 'client/src/services/settings';

export default (images: ICaptureResult[] = [], onSave: IOnSave) => {
  const composeEnhancers = (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

  let currentMode: APP_MODES = 'preview';

  if (images.length === 0 || appSettings.userOptions.startPage >= images.length) {
    currentMode = 'capture';
  }

  if (
    appSettings.userOptions.startPage === appSettings.userOptions.maxImagesCount &&
    images.length === appSettings.userOptions.maxImagesCount
  ) {
    currentMode = 'preview';
  }

  let startingPage = -1;

  if (images.length) {
    startingPage = Math.max(0, Math.min(appSettings.userOptions.startPage, images.length - 1));
  }

  const store = createStore(
    reducer,
    {
      ...INITIAL_STATE,
      images,
      currentMode,
      currentImageIndex: startingPage,
      currentCameraIndex: +localStorage.getItem('abbyy:mwc:cameraIndex'),
    },
    composeEnhancers(applyMiddleware(onSaveMiddleware(onSave), onSwitchCamera()))
  );

  return store;
};
