import React from 'react';

import './toolbarButton.scss';

interface IToolBarButtonProps {
  title: string;
  onClick?: () => void;
  image: string;
  disabled?: boolean;
}

const ToolBarButton = ({ title, onClick, image, disabled }: IToolBarButtonProps) => {
  const className =
    `ABBYY_Capture_Module__ToolBar_button ABBYY_Capture_Module__ToolBar_button_img--${image}` +
    (disabled ? ' ABBYY_Capture_Module__ToolBar_button--disabled' : '');

  return (
    <div className={className} onClick={onClick}>
      {title}
    </div>
  );
};

export default ToolBarButton;
