import React from 'react';

import './messagePage.scss';

interface IMessagePage {
  message: string;
  iconClassName?: string;
  className?: string;
}

const MessagePage = ({ message, iconClassName = '', className = '' }: IMessagePage) => {
  const messagePageClass: string = 'ABBYY_Capture_Module__MessagePage ' + className;
  const iconClass: string = 'ABBYY_Capture_Module__MessagePage-Icon ' + iconClassName;
  return (
    <div className={messagePageClass} data-testid="message-page-message">
      <div className={iconClass} />
      <span className="ABBYY_Capture_Module__MessagePage-Text">{message}</span>
    </div>
  );
};

export default MessagePage;
