/** rotateImage image by 90 deg  clockwise or counterclockwise */
import { appSettings } from 'client/src/services/settings';
import { IPoint } from 'client/src/types';
import { base64JpegToUint8, uint8ToBase64Jpeg } from 'client/src/utils/base64Conversion';
import { ICaptureResult } from 'client/src/types/declaration';
import { isEdgesFound } from './edges';
import { pointsToArray } from 'client/src/services/wasm/utils';

/**
 * @param dataURL
 * @param counterclockwise
 *
 * @return jpeg base64 string
 */
export const rotateImage = async (dataURL: string, counterclockwise: boolean = false) => {
  return new Promise<{
    imgData: string;
    width: number;
    height: number;
  }>((resolve) => {
    const img = new Image();

    img.onload = () => {
      const canvas: HTMLCanvasElement = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const rotateAngle = counterclockwise ? -Math.PI / 2 : Math.PI / 2;

      canvas.width = img.naturalHeight;
      canvas.height = img.naturalWidth;

      ctx.translate(canvas.width / 2, canvas.height / 2);
      ctx.rotate(rotateAngle);
      ctx.drawImage(img, -img.naturalWidth / 2, -img.naturalHeight / 2, img.naturalWidth, img.naturalHeight);
      ctx.rotate(-rotateAngle);
      ctx.translate(-canvas.width / 2, -canvas.height / 2);

      resolve({
        imgData: canvas.toDataURL('image/jpeg', appSettings.userOptions.jpegQuality / 100),
        width: canvas.width,
        height: canvas.height,
      });
    };

    img.src = dataURL;
  });
};

/**
 *
 * @param {IPoint[]} points
 * @param {boolean} counterclockwise
 * @param {number} width - original width of image (not cropped)
 * @param {number} height- original height of result image ((not cropped))
 */
const rotateResultPoints = (points: IPoint[], counterclockwise: boolean, width: number, height: number): IPoint[] => {
  return points.map((p) => {
    // translated to origin
    const translatedPoint = {
      x: p.x - height / 2,
      y: p.y - width / 2,
    };

    // counterclockwise (−y,x)
    // clockwise (y,−x)
    return {
      x: (counterclockwise ? translatedPoint.y : -translatedPoint.y) + width / 2,
      y: (counterclockwise ? -translatedPoint.x : translatedPoint.x) + height / 2,
    };
  });
};

export const rotateResult = async (
  captureResult: ICaptureResult,
  counterClockwise: boolean = false
): Promise<ICaptureResult> => {
  const { imgData: originalBase64Rotated, width, height } = await rotateImage(
    uint8ToBase64Jpeg(captureResult.originalImage),
    counterClockwise
  );
  const { imgData: capturedBase64Rotated } = await rotateImage(
    uint8ToBase64Jpeg(captureResult.capturedImage),
    counterClockwise
  );

  const rotatedResult = {
    ...captureResult,
    originalImage: base64JpegToUint8(originalBase64Rotated),
    capturedImage: base64JpegToUint8(capturedBase64Rotated),
  };

  if (isEdgesFound(pointsToArray(captureResult.documentBoundary))) {
    rotatedResult.documentBoundary = rotateResultPoints(
      captureResult.documentBoundary,
      counterClockwise,
      width,
      height
    );
  }
  if (isEdgesFound(pointsToArray(captureResult.originalDocumentBoundary))) {
    rotatedResult.originalDocumentBoundary = rotateResultPoints(
      captureResult.originalDocumentBoundary,
      counterClockwise,
      width,
      height
    );
  }

  return rotatedResult;
};
