import { Store, AnyAction, Dispatch } from 'redux';
import actionTypes from '../actions/actionTypes';
import { IAppStoreState } from 'client/src/types/declaration';

export default () => {
  return (store: Store<IAppStoreState>) => (next: Dispatch<AnyAction>) => (action: AnyAction) => {
    if (action.type === actionTypes.SWITCH_CAMERA) {
      localStorage.setItem('abbyy:mwc:cameraIndex', `${action.newCameraIndex}`);
    }

    return next(action);
  };
};
